'use strict';

import {isUndefined, isJq, isArray, isFunction, isJson} from "../common/util";

// 验证用户传入的初始化数据是否合法
export default data => {
	let {columns, el, pagination, ajax, mounted, removed, pageSize} = data;
	//
	!isJq(el) && (data.el = $(el));
	// 判断列类型(Array)
	if (!isArray(columns)) {
		throw new TypeError("table初始化错误! 参数columns必须是数组类型;  当前columns==" + columns);
	}
	// 判断表格最小列数
	if (!columns.length) {
		throw new TypeError("table初始化错误! 表格必须至少定义一列; 当前columns==" + columns);
	}
	// 判断表格容器唯一性
	let _$wrapper = $(el);
	if (!isJq(_$wrapper) || _$wrapper.length !== 1) {
		throw new TypeError("table初始化错误!容器不唯一或者不存在;  当前el==" + el);
	}
	// 判断mounted方法
	if (mounted && !isFunction(mounted)) {
		throw new TypeError("table初始化错误! 参数mounted的值必须是function; 当前mounted==" + mounted);
	}
	// 判断removed方法
	if (removed && !isFunction(removed)) {
		throw new TypeError("table初始化错误! 参数removed的值必须是function; 当前removed==" + removed);
	}
	// 判断ajax参数
	if (ajax && !isJson(ajax)) {
		throw new TypeError("table初始化错误! 参数ajax的值必须是json; 当前ajax==" + ajax);
	}
	// return
	return true;
}
