'use strict';


import Base from "./common/Base";

const panelBodyClass = "v-panel-body",
	panelHeaderClass = "v-panel-header",
	panelClass = "v-panel",
	panelActiveClass = "collapseOpen",
	panelOnlyOneClass = "onlyOne"


//面板收起
const panelClose = $closePanel => {
	for (let child of $closePanel) {
		let $closePanelBody = $(child).children(`.${panelBodyClass}`);
		$closePanelBody.slideUp(300, () => {
			$(child).removeClass(`${panelActiveClass}`);
		});
	}
};

//面板展开
const panelOpen = $openPanel => {
	let $openPanelBody = $openPanel.children(`.${panelBodyClass}`);
	$openPanelBody.slideDown(300, () => {
		$openPanel.addClass(`${panelActiveClass}`);
	});
};

// 面板头 的 绑定折叠事件
const onePanel = $group => {
	//绑定折叠事件
	$group.off('click', `> .${panelClass} .${panelHeaderClass}`);
	$group.on('click', `> .${panelClass} .${panelHeaderClass}`, e => {
		let $panel = $(e.target).closest(`.${panelClass}`);
		let onlyOne = $group.hasClass(`${panelOnlyOneClass}`);
		let $siblingsPanel = $panel.siblings(`.${panelClass}`);
		if ($panel.hasClass(`${panelActiveClass}`)) {
			//如果是展开状态，点击 收起 自己
			panelClose($panel);
		} else {
			//如果是收起状态，点击 展开 自己
			if (onlyOne) {
				//如果设置了只展开一个，收起其他
				panelClose($siblingsPanel);
			}
			panelOpen($panel);
		}
	})
};

//面板 默认展开项 初始化
const collapseDefault = $group => {
	let onlyOne = $group.hasClass(`${panelOnlyOneClass}`);
	if (onlyOne) {
		let $allPanel = $group.children(`.${panelClass}`);
		//默认展开 v-panel.collapseOpen， 多个就按最后一个，没有就按第一个
		let $allOpen = $group.children(`.${panelClass}.${panelActiveClass}`);

		//面板 的自适应父级失效
		$allPanel.removeClass('fillparent');
		//需要展开的面板
		let $openPanel;
		if ($allOpen.length === 0) {
			//没有设置，默认展开第一个
			$openPanel = $allPanel.eq(0);
		} else if ($allOpen.length === 1) {
			//设置了一个，展开设置的那个
			$openPanel = $allOpen;
		} else {
			//设置了多个，按设置的最后一个算
			$openPanel = $allOpen.eq($allOpen.length - 1);
		}
		//只展开需要展开的
		$allPanel.removeClass(`${panelActiveClass}`);
		$openPanel.addClass(`${panelActiveClass}`);
	}
};

//一个 面板组 的方法
const oneGroup = $group => {
	let $allPanel = $group.children(`.${panelClass}`);
	//所有面板组内 的面板 都要加 折叠按钮
	let $collapseIcon = $('<div class="v-panel-collapseIcon"><i class="vicon vicon-arrright"></i></div>');
	$collapseIcon.appendTo($allPanel.children(`.${panelHeaderClass}`));

	let fillGroup = $group.hasClass('fillGroup');
	if (fillGroup) {
		//如果设置面板组充满父级，则设置面板同时只能展开一个
		$group.addClass(`${panelOnlyOneClass}`);
		//设置面板体的高度 获取面板头的高度，所有面板体的高度 = 面板组的高度 - 所有面板头
		let penelCount = $allPanel.length;
		let headH = $allPanel.eq(0).children(`.${panelHeaderClass}`).outerHeight();
		let bodyH = $group.height() - headH * penelCount;
		$allPanel.children(`.${panelBodyClass}`).outerHeight(bodyH);
	}

	//面板 默认展开项 初始化
	collapseDefault($group);

	//绑定事件
	onePanel($group);
};

//初始化
const panel = (data) => {
	let {$collapseHolder} = data;
	let $groups = $('.v-panelCollapse ', $collapseHolder);
	//循环每个 面板组
	for (let group of $groups) {
		oneGroup($(group));
	}
};



export default panel;




