'use strict';

import {isJson, isString} from "../common/util";

// 查询字符串转json，例如 a=1&b=2 ==> {a:1,b:2}
export const queryStringToJson = queryString => {
	if (isString(queryString)) {
		if (queryString.length === 0) return {};
		let args = {};
		let arr = queryString.split('&');
		arr.forEach(item => {
			let itemArr = item.split("="),
				decodeKey = itemArr[0],
				decodeVal = /%[0-9a-fA-F]{2,}/.test(itemArr[1]) ? decodeURIComponent(itemArr[1]) : itemArr[1];
			if (decodeKey) {
				args[decodeKey] = decodeVal;
			} else {
				console.error(`待转换字符串：${queryString}`);
				throw new Error(`query string转json出现错误，${item}部分被忽略`);
			}
		});
		return args;
	}
	throw new Error(`${queryString}无法转换为json!`);
}

// json转查询字符串，例如 {a:1,b:2} ==> a=1&b=2
export const jsonToQueryString = (json, encode = false) => {
	if (isJson(json)) {
		let arr = [];
		Object.keys(json).map(key => {
			// 非字母数字下划线key报错
			if (/\W/.test(key)) {
				throw new Error(`json转换失败，禁止${key}作为key值;`);
			}
			let val = encode ? encodeURIComponent(json[key]) : json[key];
			arr.push(`${key}=${val}`);
		})
		return arr.join("&");
	} else {
		let log = `json转换query string失败;
----- string start -----
${json}
----- string  end -----`;
		throw new Error(log);
	}
}

