'use strict';

import {isString} from "./util";

const loadBodyHtml = async page => {
	return await new Promise((resolve, reject) => {
		// default pageType
		// page.dataType = page.dataType || "html";
		// page is string
		isString(page) && (page = {url: page});
		//
		let loader = $.ajax(page);
		loader.then(htmlText => {
			let realHtmlText = htmlText;
			if (htmlText.indexOf("<body") !== -1 && htmlText.indexOf("</body>") !== -1) {
				// 匹配body内html
				let REG_BODY = /<body[^>]*>([\s\S]*)<\/body>/i;
				let result = REG_BODY.exec(realHtmlText);
				if (result && result.length === 2)
					realHtmlText = result[1];
				// 获得body内的html
			}
			resolve($(realHtmlText));
		}, (XMLHttpRequest, textStatus, thrownError) => {
			reject(thrownError);
			throw new Error(`加载${url}失败`);
		})
	})
}

export default loadBodyHtml;
