'use strict';

const vHistory = {
	pushState({module, realModule, params, hash, pageSymbol, fromSymbol}) {
		this.history.pushState({module, realModule, params, hash, pageSymbol, fromSymbol}, "", hash);
	},
	replaceState({module, realModule, params, hash, pageSymbol, fromSymbol}) {
		this.history.replaceState({module, realModule, params, hash, pageSymbol, fromSymbol}, "", hash);
	},
	get state() {
		if (history && history.pushState && history.replaceState) {
			return history.state;
		} else if (window.History) {
			return window.History.getState().data;
		}
	},
	get history() {
		if (!this._history) {
			if (history && history.pushState && history.replaceState) {
				// console.log("window.history");
				this.history = window.history;
			} else if (window.History) {
				// console.log("history.js");
				this.history = window.History;
			} else {
				throw new Error("当前浏览器不支持原生路由, 请在页面顶端引入History.js");
			}
		}
		return this._history;
	},
	set history(historyVal) {
		this._history = historyVal;
	}
}


export default vHistory;