'use strict';
import Base from "./common/Base";
import {isFunction, isJq, isJson, isString} from "./common/util";
import tpl from "./common/tpl";
import _vName from "./componentName";
import html from '../../template/info.html';
//
// showClose
// closable
// type(popupSuccess info warning error)
// closeText
// showIcon
const defaultHideDelaySeconds = 2.5,
	/*fadeOutClass = "v-info-out",
	fadeInClass = "v-info-in",*/
	fadeOutClass = "vAniMsgOut",
	fadeInClass = "vAniMsgIn",
	textWrapClass = "v-info-text";

// delay hide handler
const delayHide = _root => {
	let {$target, seconds = defaultHideDelaySeconds} = _root._data;
	let timeoutId = $target.data("v_delayHideTimeOutId");
	clearTimeout(timeoutId);
	$target.data("v_delayHideTimeOutId", setTimeout(() => {
		removeHandler(_root);
	}, seconds * 1000));
}

// remove handler
const removeHandler = _root => {
	let {$target} = _root._data;
	$target.addClass(`${fadeOutClass}`);
	$target.removeClass(`${fadeInClass}`);
	setTimeout(() => {
		$target.remove();
		_root.superRemove();
	}, 300);
}

// close handler
const closeHandler = _root => {
	let {$target, customData: {showClose}} = _root._data;
	if (showClose) {
		let $closeBtn = $target.find(".v-info-closeBtn");
		$closeBtn.on("click", e => {
			let timeoutId = $target.data("v_delayHideTimeOutId");
			clearTimeout(timeoutId);
			removeHandler(_root);
		});
	}
}

// Message
class Message extends Base {
	constructor(ops) {
		super();
		this._data = {};
		//
		let $template = $(tpl(html));
		let $i = $template.find(".v-info-icon>i");
		let currType = "info";
		if (isJson(ops)) {
			if (ops.content) {
				let {content, showClose = true, type = "success", showIcon = true, closeText, closeCallback} = ops;
				//
				this._data.customData = {content, showClose, type, showIcon, closeText, closeCallback};
				let $txtWrap = $template.find(`.${textWrapClass}`)
				if (isString(content)) {
					$txtWrap.html(content);
				} else if (isFunction(content)) {
					content($txtWrap, this);
				} else {
					throw new TypeError(`v.${type}()参数错误，content属性不能为空`);
				}
				currType = type;
			} else {
				throw new TypeError(`v.${type}()参数错误，content属性不能为空`);
			}
		} else if (isString(ops)) {
			$template.find(`.${textWrapClass}`).html(ops);
			currType = "info";
			this._data.customData = {content: ops, showClose: true, type: "success", showIcon: true};
		} else {
			let {type = "success"} = ops;
			throw new TypeError(`v.${type}()参数类型错误`);
		}
		//
		$template.addClass(`${_vName}-info-${currType}`);
		// append dom
		this._data.$target = $template;
		$template.appendTo("body");
		//
		//$template.addClass("v-info-in");
		$template.addClass(`${fadeInClass}`);
		//
		closeHandler(this);
		//
		delayHide(this);
	}

	remove() {
		removeHandler(this);
	}

	superRemove() {
		super.remove();
	}
}

//
const message = {
	success(ops) {
		let {content, showClose, showIcon, closeText, closeCallback} = ops;
		ops.type = "success";
		return new Message(ops);
	},

	error(ops) {
		let {content, showClose, showIcon, closeText, closeCallback} = ops;
		ops.type = "error";
		return new Message(ops);
	},

	info(ops) {
		let {content, showClose, showIcon, closeText, closeCallback} = ops;
		ops.type = "info";
		return new Message(ops);
	},

	warning(ops) {
		let {content, showClose, showIcon, closeText, closeCallback} = ops;
		ops.type = "warning";
		return new Message(ops);
	},

	remove(ops) {
		removeHandler(ops);
	}
}
//
export default message;
