// 常规路由-替换成方法验证
// const normalHashReg = /^#\/\w+$|^#\/?(?:\w+\/)+\w*$|^#\/?(?:\w+\/)+(?:\w+=[\w-.&%]*)+$/;
// 删除参数路由
// let deleteParamsHashReg = /^#deleteParams\((?:\w+,?)+\)$/;
// 设置参数路由
let setParamsHashReg = /^#setParams\({(['"]?\w+['"]?:['"]?[\w-.&%]+['"]?,?)+}(,(true|false))?\)$/;
const validModuleReg = /^\/?(\w+\/?)+/;
const invalidModuleReg = /\W/;
const invalidHashReg = /[^\w\d&%=#.\/]|http|www/gi;
const anchorHashReg = /^(\w+\/?)*$/;

const normalHashTest = ({_root, currHash}) => {
	let {module, params, hash} = hashToJson(currHash);
	if (!anchorHashReg.test(module)) {
		let invalidStr = module.match(invalidModuleReg);
		throw new Error(`模块里不允许有 ${invalidStr}`);
	}
	return validateModule({_root, module, params, hash}) && validateParams({_root, module, params, hash});
}

