// *****************************************0.3*****************************************
// 将hash地址转成json对象
const hashToJson = (hash) => {
	let defaultParams = {};
	// parse module&&params
	let module, params = {};
	// moduleHash && params
	if (/\/\w+=/.test(hash)) {
		module = hash.replace(/(^#\/?)(.*)(?=\/).*/, "$2");
		try {
			// query String
			let queryString = hash.replace(/(.*\/)/, "");
			// parse query String
			params = stringToJson(queryString);
		} catch (e) {
			let queryString = hash.match(invalidHashReg);
			throw new Error(`路由参数${queryString}解析失败`);
		}
	} else {
		module = hash.replace(/^#\/?|\/$/g, "");
		// none
		params = defaultParams;
	}
	// return
	return {module, params, hash}
}

