// add click listener
const clickListener = (_root) => {
	let domFilter = "a[href^='#'],*[data-router]";
	let $body = globalData.$body;
	//
	$body.on("click", "a[href='#']", function (e) {
		return false;
	})
	// dom with property data-router click
	$body.on("click", domFilter, function (e) {
		let $this = $(this),
			{_curr} = _root._data,
			// {module, params, hash, pageSymbol, fromSymbol} = vHistory.state,
			currHash = $this.attr("data-router") || $this.attr("href");
		// click handler
		if (normalHashTest({_root, currHash})) {// normal hash
			// parse current hash
			let {module, params, hash} = hashToJson(currHash);
			// validate module&&params
			if (validateModule({_root, module, params, hash}) && validateParams({_root, module, params, hash})) {// 合法模块&&参数
				// 验证权限&&执行
				verifyPermissionHandler({_root, module, params}).then(
					() => {// 有执行权限
						let viewType = "newPage", setState = "push";
						// dataIframeSrc
						let iframeSrc = $this.attr("data-v-iframe-url");
						if (iframeSrc && validateIframeUrl({_root, iframeSrc})) {
							// have data-v-iframe-url
							params.vIframeUrl = iframeSrc
						}
						let newRoutingNode = new RoutingNode({_root, module, params});
						newRoutingNode.enter({viewType, setState, triggerEvent: "click"}).then(() => {

						});
					},
					() => { //没有执行权限

					}
				)
			}
			// prevent default
			e.preventDefault();
		} else if (setParamsHashReg.test(currHash)) { // set params ?????????????????????????????0.3版本尚未改完?????????????????????????????
			// console.log("设置参数路由");
			if (_curr) {
				let commandString = currHash.replace(/^#setParams\(/, "").replace(/\)$/, "");
				let params = stringToJson(commandString);
				_curr.setParams(params);
			} else {
				throw new Error("不存在当前路由节点, 设置参数失败");
			}
			// prevent default
			e.preventDefault();
		} else if (currHash !== "#" && !anchorHashReg.test(currHash)) { //?????????????????????????????0.3版本尚未改完?????????????????????????????
			let invalidStr = currHash.match(invalidHashReg);
			throw new Error(`路由信息里不允许有 ${invalidStr}`);
		}
	});
}

// add hash change listener
const popStateListener = (_root) => {
	// addEventListener(ie10+, chrome5.0+, firefox4.0+, ios5.0+, android 4.2+)
	window.addEventListener("popstate", (e) => {
		if (e.state) {
			let {module, params, hash, pageSymbol, fromSymbol, vIgnoreNode} = e.state;
			// 是否忽略该节点
			if (vIgnoreNode) {
				return false;
			}
			// set fromSymbol ??????????????????????????????????????????????????????????
			!fromSymbol && (fromSymbol = storage.get("v_router_lastPageSymbol"));
			// 验证模块
			if (!validateModule({_root, module, params, hash})) {
				// 非法module,直接返回,验证方法会抛出异常
				return false;
			}
			// 验证参数
			if (!validateParams({_root, module, params, hash})) {
				// 非法params,直接返回,验证方法会抛出异常
				return false;
			}
			// 验证权限
			verifyPermissionHandler({_root, module, params}).then(() => {
				let viewType = "auto", setState = false;
				let newRoutingNode = new RoutingNode({_root, module, params});
				newRoutingNode.enter({viewType, setState, triggerEvent: "popstate"}).then(() => {

				});
			})
		}
	});
	// ie9
	if (globalData.browserName === "ie9") {
		throw new Error(`不支持在 ${globalData.browserName} 浏览器上使用路由!`);
	}
}

class AmdRouter {
	constructor(customData = {}) {
		if (!AmdRouter._instance) {
			// require requireJs
			if (!isFunction(customData.requireJs)) {
				throw new Error("路由初始化参数必须有requireJs");
			}
			//save requirejs
			globalData.requireJs = customData.requireJs;
			this._data = {customData};
			AmdRouter._instance = this;
			// set module proxy
			rModule.moduleProxy = isJson(customData.moduleProxy) ? customData.moduleProxy : {};
		}
		return AmdRouter._instance;
	}

	// 初始化
	async init() {
		//初始化
		let _root = this, state = vHistory.state;
		// await
		await new Promise((initResolve, reject) => {
			if (state) {
				let {module, params, vIgnoreNode = null} = state;
				// 验证模块&&参数
				if (validateModule({_root, module, params}) && validateParams({_root, module, params})) {// 合法模块&&参数
					// 验证权限&&执行
					verifyPermissionHandler({_root, module, params}).then(() => {// 有执行权限(刚执行过该节点,用户刷新了)
						refreshCallback({_root});
						let viewType = "auto", setState = "auto";
						let newRoutingNode = new RoutingNode({_root, module, params});
						newRoutingNode.enter({viewType, setState, triggerEvent: "refresh"}).then((resolve, reject) => {
							initResolve("当前路由合法,用户刷新了");
						});
					})
				} else {// 非法module||params
					if (vIgnoreNode === true) {// 是忽略节点(刚执行过该节点,然后用户刷新了)
						refreshCallback({_root});
					} else if (vIgnoreNode === null) {// 没有忽略标识,需要执行默认路由
						console.log("没有忽略标识,需要执行默认路由");
						executeDefaultRouter({_root}).then(() => {
							initResolve();
						});
					}
				}
			} else { // first page
				// parse current hash
				let {module, params = {}, hash} = hashToJson(location.hash);
				// validate module&&params
				if (validateModule({_root, module, params, hash}) && validateParams({_root, module, params, hash})) {// 合法模块&&参数
					// 验证权限&&执行
					verifyPermissionHandler({_root, module, params}).then(() => {// 有执行权限
						let viewType = "firstPage", setState = "push";
						let newRoutingNode = new RoutingNode({_root, module, params});
						newRoutingNode.enter({viewType, setState, triggerEvent: "fistPage"}).then(() => {
							initResolve("first page, 当前hash有效, 并且有执行权限");
						});
					})
				} else {// 非法module||params, 需要执行默认路由
					// console.log("非法module||params, 需要执行默认路由");
					executeDefaultRouter({_root}).then(() => {
						initResolve();
					});
				}
			}
		})
		// add listener
		clickListener(_root);
		popStateListener(_root);
	}

	goto(data) {
		let _root = this;
		let {module, params} = data;
		// 验证模块
		if (!validateModule({_root, module, params})) {
			// 非法module,直接返回,验证方法会抛出异常
			return false;
		}
		// 验证参数
		if (!validateParams({_root, module, params})) {
			// 非法params,直接返回,验证方法会抛出异常
			return false;
		}
		// 验证权限
		verifyPermissionHandler({_root, module, params}).then(() => {
			let viewType = "auto", setState = "auto";
			let newRoutingNode = new RoutingNode({_root, module, params});
			newRoutingNode.enter({viewType, setState, triggerEvent: "goto"}).then(() => {

			});
		})
	}

	setParams(data, replaceParams) {
		this._data._curr.setParams(data, replaceParams);
	}

	paramsChange(options) {
	}

	// 设置代理后的实际模块路径
	set realModule(newModule) {
		this._data._realModule = newModule;
	}

	// 获得代理后的实际模块路径
	get realModule() {
		return this._data._realModule;
	}

	// 模块
	set module(newModule) {
		this._data._module = newModule;
	}

	// 模块
	get module() {
		//某些情况可能会导致module不存在(当前用户没有执行权限||忽略节点)
		return this._data && this._data._module ? this._data._module : "";
	}

	get moduleArr() {
		return this.module.split("/").slice(0);
	}

// set access method has type that is not compatible with get accessor type,expecting "*"
	set params(newParams) {
		this._data._params = newParams;
	}

	get params() {
		return this._data._params;
	}

	set model(newModel) {
		this._data._model = newModel;
	}

	get model() {
		return this._data._model;
	}
}

export default AmdRouter;
