'use strict';
import {isFunction, isArray, isString} from "./util";

const onceCallbackKey = "__onceCallback",
	_eventData = "__eventData";

export default class VEvent {
	constructor() {
		this[_eventData] = {
			cbs: {},
			cData: undefined
		}
	}

	on(type, cb) {
		if (type && isFunction(cb)) {
			let {cbs} = this[_eventData];
			cbs[type] ? cbs[type].push(cb) : cbs[type] = [cb];
		}
	}

	once(type, cb) {
		if (type && isFunction(cb)) {
			let {cbs} = this[_eventData];
			cbs[type] ? cbs[type].push(cb) : cbs[type] = [cb];
			cbs[onceCallbackKey] = cb;
		}
	}

	off(type, cb) {
		if (type) {
			let {cbs} = this[_eventData];
			if (isFunction(cb)) {
				let cbsArr = cbs[type];
				if (cbsArr) {
					cbsArr.forEach((currCb, i) => {
						if (cb === currCb) {
							cbsArr.splice(i, 1);
						}
					})
				}
			} else {
				delete cbs[type];
			}
		} else {
			this[_eventData].cbs = {};
		}
	}

	trigger(type, data) {
		if (data && !isArray(data)) {
			throw new Error("trigger()第2个参数必须是Array");
		}
		//
		if (isString(type)) {
			let {cbs} = this[_eventData];
			if (cbs[type]) {
				cbs[type].forEach((cb) => {
					let currentTarget = this;
					let e = [{type, currentTarget}];
					let args = e.concat(data);
					cb.apply(this, args);
				})
				// 处理单次事件
				if (cbs[onceCallbackKey]) {
					delete cbs[onceCallbackKey];
					this.off(type, onceCallbackKey);
				}
			}
		}
	}

	// set data(val) {
	// 	if (isJson(val)) {
	// 		this[_eventData].cData = val;
	// 	}
	// }
	//
	// get data() {
	// 	return this[_eventData].cData;
	// }
}
