'use strict';

import {isJq, isJson} from "./common/util";

// validate
const validate = ({el, rules}) => {
	if (!isJq(el)) {
		throw new TypeError(`v.validate()参数类型错误. el必须是jquery对象,当前el=${el}`);
	}
	if (!isJson(rules)) {
		throw new TypeError(`v.validate()参数类型错误. rules必须是json对象,当前rules=${rules}`);
	}
	let $wrap=$(el);
	//
	$wrap.find()

}


export default validate;