'use strict';

import Tab from "./Tab";
import Popup from "./Popup";
import Table from "./Table_v3";
import Pagination from "./Pagination";
import Vdrag from "./Vdrag";
import VEvent from "./common/VEvent";

const event = () => {
	return new VEvent();
}

const pagination = ops => {
	return new Pagination(ops);
}

const popup = ops => {
	return new Popup(ops);
}


const tab = ops => {
	return new Tab(ops);
}

const table = ops => {
	return new Table(ops);
}

const drag = ops => {
	return new Vdrag(ops);
}

const eventBus = new VEvent();

export {pagination, popup, tab, table, drag, event, eventBus}
