'use strict';

import {isDetached} from "./util";
import isMounted from "./isMounted";

const resizeDelay = 250;// 监听间隔毫秒

// resizeListener
const addResizeListener = ($this, cb, isMounted) => {
	let cw, ch, callbackArr = [], intervalId;
	// resizeListener
	const resizeListener = () => {
		let newW = $this.width();
		let newH = $this.height();
		if (newW !== cw || newH !== ch) {
			callbackArr.forEach(item => {
				cw = newW;
				ch = newH;
				if (cw !== 0 && ch !== 0) {
					item(cw, ch);
				} else {
					if ($this.closest("body").length === 1) {
						item(cw, ch);
					} else { // dom被删除了
						// console.log("dom remove");
						intervalId && clearInterval(intervalId);
					}
				}
			})
		}
	}

	// 初始化赋值
	if (!cw && !ch) {
		cw = $this.width();
		ch = $this.height();
		// isMounted
		isMounted && cb(cw, ch);
		//
		intervalId = setInterval(() => {
			resizeListener();
		}, resizeDelay)
	}
	callbackArr.push(cb);
}

// 等待dom挂载以后开始尺寸监听。如果一直不挂载，等待1分钟后删除该监听
const onResize = ($this, cb) => {
	if (!isDetached($this)) { // dom已挂载,执行resizeListener
		addResizeListener($this, cb);
	} else { // dom尚未挂载,等待挂载以后执行resizeListener
		isMounted($this).then(() => { // dom挂载完毕
			// let id = $this.attr("id");
			// console.log(`$("#${id}")已经挂载`);
			addResizeListener($this, cb, true);
		}).catch(() => {
			$this.trigger("mountTimeout");
			// let id = $this.attr("id");
			// console.error(`v.onResize($("#${id}"))等待超时`);
		})
	}
}


export default onResize;
