'use strict';


// remove some wrap
const removeWrap = $wrap => {
	$wrap.find(".v-table-title-wrap").remove();
	$wrap.find(".v-table-config-wrap").remove();
}

// create wrap
const setEachWraps = _root => {
	let {_data, _data: {leftColumns, rightColumns, bodyColumns, $wrap, initData: {header, pagination, config}}} = _root;
	// --------------------- 通用容器 ---------------------
	let _$footerLoading = $wrap.find(".v-table-footer-loading"),
		_$footerRefresh = $wrap.find(".v-table-footer-loading"),
		_$footerOrder = $wrap.find(".v-table-footer-order"),
		// 表格容器 **************************************************
		_$tableWrapper = $wrap.find(".v-table-wrap");
	// save
	$.extend(true, _data, {_$footerLoading, _$footerRefresh, _$footerOrder, _$tableWrapper});

	// --------------------- 分页容器 ---------------------
	let _paginationWrap = $wrap.find(".v-table-pagination-wrap");
	if (pagination) {
		_root._data._paginationWrap = _paginationWrap
	} else {
		_paginationWrap.remove();
	}

	// --------------------- 表格标题容器 ---------------------
	let _$tableTitle = $wrap.find(".v-table-title-wrap"),
		_$titleLeft = $wrap.find(".v-table-title-left"),
		_$titleRight = $wrap.find(".v-table-title-right");
	if (header) {
		$.extend(true, _data, {_$tableTitle, _$titleLeft, _$titleRight});
	} else {
		_$tableTitle.remove();
		_$titleLeft.remove();
		_$titleRight.remove();
	}

	// --------------------- 表格左侧容器 ---------------------
	let _$lWrap = $wrap.find(".v-table-left-wrap");
	if (leftColumns.length > 0) {
		let _$lHWrap = _$lWrap.find(".v-table-left-header"),
			_$lBWrap = _$lWrap.find(".v-table-left-body"),
			_$lHTable = _$lHWrap.find("table"),
			_$lBTable = _$lBWrap.find("table"),
			_$lHThead = _$lHTable.find("thead"),
			_$lBTbody = _$lBTable.find("tbody"),
			_$lHColgroup = _$lHTable.find("colgroup"),
			_$lBColgroup = _$lBTable.find("colgroup");
		// save
		$.extend(true, _data, {
			_$lWrap, _$lHWrap, _$lBWrap, _$lHTable, _$lBTable,
			_$lHThead, _$lBTbody, _$lHColgroup, _$lBColgroup
		});
	} else {
		_$lWrap.remove();
	}
	// --------------------- 主体表格容器 ---------------------
	if (bodyColumns.length > 0) {
		let _$cWrap = $wrap.find(".v-table-center-wrap"),
			_$cHWrap = _$cWrap.find(".v-table-center-header"),
			_$cHScrollWrap = _$cWrap.find(".v-table-center-header-body"),
			_$cBWrap = _$cWrap.find(".v-table-center-body"),
			_$noDataWrap = $wrap.find(".v-table-no-data"),
			_$cHTable = _$cHWrap.find("table"),
			_$cBTable = _$cBWrap.find("table"),
			_$cHThead = _$cHTable.find("thead"),
			_$cBTbody = _$cBTable.find("tbody"),
			_$cHColgroup = _$cHTable.find("colgroup"),
			_$cBColgroup = _$cBTable.find("colgroup");
		// save
		$.extend(true, _data, {
			_$cWrap, _$cHWrap, _$cHScrollWrap, _$cBWrap, _$noDataWrap, _$cHTable, _$cBTable,
			_$cBColgroup, _$cHColgroup, _$cHThead, _$cBTbody
		});
	}
	// --------------------- 右侧锁定列 ---------------------
	let _$rWrap = $wrap.find(".v-table-right-wrap");
	if (rightColumns.length > 0) {
		// 右侧
		let _$rHWrap = _$rWrap.find(".v-table-right-header"),
			_$rBWrap = _$rWrap.find(".v-table-right-body"),
			_$rHTable = _$rHWrap.find("table"),
			_$rBTable = _$rWrap.find("table"),
			// thead
			_$rHThead = _$rHTable.find("thead"),
			// tbody
			_$rBTbody = _$rBTable.find("tbody"),
			// 表头colgroup
			_$rHColgroup = _$rHTable.find("colgroup"),
			// 表体Colgroup
			_$rBColgroup = _$rBTable.find("colgroup");
		// save
		$.extend(true, _data, {
			_$rWrap, _$rHWrap, _$rBWrap, _$rHTable, _$rBTable,
			_$rHThead, _$rBTbody, _$rHColgroup, _$rBColgroup
		});
	} else {
		_$rWrap.remove();
	}
	// --------------------- 右侧锁定列 ---------------------

	// 右侧
	let _$footerWrap = $wrap.find(".v-table-footer-wrap");
	// save
	$.extend(true, _data, {_$footerWrap});
	// 	暂时删除部分容器
	removeWrap($wrap);
}

export default setEachWraps;


