'use strict';

import {isFunction} from "../common/util";

const trHoverClass = "tr-hover",
	trClass = "v-table-tr",
	notInput = ":not(input)",
	_trClass = `tr.${trClass}`,
	trSelectedClass = "v-table-tr-selected",
	eNameSpace = ".v-table";

const getData = ({_root, trIndex, e}) => {
	let $tr = _root.$allTr(trIndex),
		$leftTr = _root.$leftTr(trIndex),
		$centerTr = _root.$centerTr(trIndex),
		$rightTr = _root.$rightTr(trIndex),
		rowData = _root.rowData(trIndex),
		returnVal = {},
		{altKey, ctrlKey, shiftKey, pageX, pageY} = e;
	//
	$.extend(
		returnVal,
		{trIndex, rowData, $tr, $leftTr, $centerTr, $rightTr, altKey, ctrlKey, shiftKey, pageX, pageY}
	);
	//
	return returnVal;
}

// tableEvent
const tableEvent = _root => {
	let {_$lBTbody, _$cBTbody, _$rBTbody} = _root._data,
		{initData: {trClick, trContextMenu, trDblclick}} = _root._data,
		$tbody = _$cBTbody,
		clickTimeoutId, timeoutSeconds = 0.2;
	//
	_$lBTbody && ($tbody = $tbody.add(_$lBTbody));
	_$rBTbody && ($tbody = $tbody.add(_$rBTbody));
	// tbody length
	if ($tbody.length === 0) return false;
	// mouseenter
	$tbody.each((i, item) => {
		let $tbodyItem = $(item);
		$tbodyItem.on(`mouseenter${eNameSpace}`, _trClass, e => {
			let $tr = $(e.currentTarget);
			let trIndex = $tbodyItem.find(_trClass).index($tr);
			_root.$allTr(trIndex).addClass(trHoverClass);
		})
	})
	// mouseleave
	$tbody.each((i, item) => {
		let $tbodyItem = $(item);
		$tbodyItem.on(`mouseleave${eNameSpace}`, _trClass, e => {
			let $tr = $(e.currentTarget);
			let trIndex = $tbodyItem.find(_trClass).index($tr);
			_root.$allTr(trIndex).removeClass(trHoverClass);
		})
	})
	// click
	$tbody.each((i, item) => {
		let $tbodyItem = $(item);
		$tbodyItem.on(`click${eNameSpace}`, _trClass, e => {
			let $currTarget = $(e.currentTarget),
				$target = $(e.target),
				trIndex = $tbodyItem.find(_trClass).index($currTarget),
				$allTr = _root.$allTr(trIndex);
			//
			// -----------------行选择-----------------
			$allTr.removeClass(trHoverClass);
			// 没有按下ctrl，取消原有选定tr
			!e.ctrlKey && $tbody.children(`.${trSelectedClass}`).removeClass(trSelectedClass);
			//
			let cmd = $allTr.hasClass(trSelectedClass) ? $.fn.removeClass : $.fn.addClass;
			cmd.call($allTr, trSelectedClass);
			//
			// -----------------用户自定义方法调用-----------------
			if (isFunction(trClick)) {
				if ($target.is(notInput)) {
					if (!isFunction(trDblclick)) {
						//
						let data = getData({_root, trIndex, e});
						//
						trClick(data, _root);
					} else {
						clearTimeout(clickTimeoutId);
						clickTimeoutId = setTimeout(() => {
							//
							let data = getData({_root, trIndex, e});
							//
							trClick(data, _root);
						}, timeoutSeconds * 1000);
					}
				}
			}
		})
	})
	// dblclick -----------------用户双击方法调用-----------------
	if (isFunction(trDblclick)) {
		$tbody.each((i, item) => {
			let $tbodyItem = $(item);
			$tbodyItem.on(`dblclick${eNameSpace}`, _trClass, e => {
				let $currTarget = $(e.currentTarget),
					$target = $(e.target),
					trIndex = $tbodyItem.find(_trClass).index($currTarget);
				//
				// 用户双击方法调用
				if ($target.is(notInput)) {
					clearTimeout(clickTimeoutId);
					let data = getData({_root, trIndex, e});
					//
					trDblclick(data, _root);
				}
			})
		})
	}
	// contextmenu -----------------用户右键方法调用-----------------
	if (isFunction(trContextMenu)) {
		$tbody.each((i, item) => {
			let $tbodyItem = $(item);
			$tbodyItem.on(`contextmenu${eNameSpace}`, _trClass, e => {
				let $currTarget = $(e.currentTarget),
					$target = $(e.target),
					trIndex = $tbodyItem.find(_trClass).index($currTarget);
				//
				// 右键方法调用
				if ($target.is(notInput)) {
					let data = getData({_root, trIndex, e});
					//
					trContextMenu(data, _root);
				}
			})
		})
	}
}

export default tableEvent;



