'use strict';


import Base from "./common/Base";

// 文本域自动高度，可以设置最大高度，可以设置最大字符并显示
const textareaClassName = "textareaAuto",
	textareaParentName = "v-textarea",
	showNumberClassName = "showNumber";


//动态添加显示剩余字数的dom
const showNumberHtml = $textarea => {
	//判断dom结构是否正确，如果不正确，就动态加dom
	if($textarea.parent().hasClass(`${textareaParentName}`)){
	}else{
		$textarea.wrap(`<div class=${textareaParentName}></div>`);
	}
	let $showNumber = $("<span class='textareaNumber'><span class='already'></span> /<span class='all'></span></span>");
	$textarea.after($showNumber);
};


// 文本域 的 自动高度计算
const textareaAutoHeight = $textarea => {

	let text = $textarea[0];
	let maxL = $textarea.attr("maxlength");

	let $textareaParent,$textareaNumber,$already,$all;
	if(typeof(maxL) != "undefined" && maxL != ""){
		if(!isNaN(maxL)){
			//动态添加显示剩余字数的dom
			showNumberHtml($textarea);

			$textareaParent = $textarea.parent(`.${textareaParentName}`);
			$textareaParent.addClass(`${showNumberClassName}`);
			$textareaNumber = $textareaParent.find(".textareaNumber");
			$already = $textareaParent.find(".already");
			$all = $textareaParent.find(".all");
			$all.html(maxL);
		}
	}


	const textresize = () => {
		text.style.height = "auto";
		text.style.overflow = "hidden";
		$(text).height(text.scrollHeight);
		let maxH = $(text).css("max-height");
		if(maxH != "none"){
			text.style.overflow = "auto";
		}
		$textareaParent = $textarea.parent(`.${textareaParentName}`);
		if($textareaParent.hasClass(`${showNumberClassName}`)){
			let textL = text.value.length;
			$already.html(textL);
			if(maxL - textL < 10){
				$textareaNumber.addClass("danger");
			}else{
				$textareaNumber.removeClass("danger");
			}
		}
	};

	textresize();
	$textarea.on("input", function () {
		textresize();
	})

};



//初始化
const textarea = (data) => {
	let {$textareaHolder} = data;
	let $groups = $(`.${textareaClassName}`, $textareaHolder);
	//循环每个 文本域
	for (let $textarea of $groups) {
		textareaAutoHeight($($textarea));
	}
};






export default textarea;




