'use strict';

import {createTr} from "../table_v0.3/createTr";

class Tbody {
	constructor(_root) {
		this._data = {_root};
	}

	init() {
		let {_root} = this._data,
			{
				_data: {
					_$lBTbody, _$cBTbody, _$rBTbody,
				}
			} = _root,
			//
			$emptyWrap = _$cBTbody;
		// 左右列
		_$lBTbody && ($emptyWrap = $emptyWrap.add(_$lBTbody));
		_$rBTbody && ($emptyWrap = $emptyWrap.add(_$rBTbody));
		// empty
		$emptyWrap.empty();
		createTr(_root);
	}

	// 表格被设置新的data
	set data(newData) {
		if (isArray(newData) && newData.length > 0) {
			let {_root} = this._data,
				newDataSource = _root.dataSource;
			// change data
			newDataSource.data = newData;
			//
			_root.dataSource = newDataSource;
		} else {
			this.noDataHandler();
		}
	}

	// 无数据处理
	noDataHandler() {

	}
}

export default Tbody;
