'use strict';

// get scrollbar size
const scrollbarSize = $el => {
	let elClass = $el.attr("style");
	$el.css({"visibility": "hidden", "overflow": "scroll"});
	let scrollbarWidth = $el[0].offsetWidth - $el[0].clientWidth;
	let scrollbarHeight = $el[0].offsetHeight - $el[0].clientHeight;
	//
	elClass ? $el.attr("style", elClass) : $el.removeAttr("style");
	// return
	return {scrollbarWidth, scrollbarHeight};
}

export default scrollbarSize;
