'use strict';

import {isJson, isNumber, isEqual} from "../common/util";
import onResize from "../common/onResize";


//const
const paginationHeight = 43, // 分页高度
	tableMinWidth = 100,
	tableMinHeight = 150,
	tableTitleHeight = 40, // 扩展表格标题高度
	tableTheaderHeight = 37, // thead高度
	lineHeight = 40;


// 所有的布局相关属性都在这里设置
class Layout {
	constructor(_root) {
		let {$wrap, _$rBWrap, scrollbarWidth, rightColumns, initData: {el}} = _root._data, againTimeoutId;
		//
		this._data = {_root};
		// 根据滚动条尺寸，设置右侧容器
		rightColumns.length > 1 && scrollbarWidth !== 17 && _$rBWrap.css({right: `-${scrollbarWidth}px`});
		// resize
		onResize($wrap, () => {
			// 容器可见
			if (el.is(":visible")) {
				// 尺寸改变处理
				this.resizeHandler();
				// again
				let delayResize = () => {
					againTimeoutId && clearTimeout(againTimeoutId);
					againTimeoutId = setTimeout(() => {
						let {colgroup} = this._data._root._data;
						colgroup.resize();
					}, 10);
				}
				// 延迟处理
				delayResize();
			}
		})
	}

	// 设置容器尺寸
	resizeHandler() {
		let {
			initData: {pagination}, $wrap, _$tableWrapper, _$lWrap, _$rWrap, _$cBWrap, _$cBTable,
			_$cHScrollWrap, $el, scrollbarHeight, scrollbarWidth, dataSource, leftColumns, rightColumns
		} = this._data._root._data;
		let height = 0, tbodyHeight, bottom = 0, right = 0;
		//
		let _cBWrap = _$cBWrap[0];
		// 判断滚动条
		let horizontal = _cBWrap.clientHeight !== _cBWrap.offsetHeight;
		let vertical = _cBWrap.clientWidth !== _cBWrap.offsetWidth;
		//
		// 根据分页情况分别进行容器尺寸设置
		if (pagination) { // 有分页
			if (!isNumber(pagination.pageSize)) { // 根据满屏表格规则设置容器
				height = $el.height() - paginationHeight;
			} else { // 根据用户自定义pageSize设置容器
				tbodyHeight = pagination.pageSize * lineHeight;
				height = tbodyHeight + tableTheaderHeight + 1;
				// 自定义pageSize，需要设置外围容器高度，否则高度不正确
				$wrap.css({height: height + paginationHeight});
			}
		} else { // 无分页
			let rowsNumber = dataSource.data.length;
			// rows height
			height += rowsNumber * lineHeight;
			// theader height
			height += tableTheaderHeight;
			// border height
			height++;
			// 按照实际高度渲染，可能由于内容多导致多行高度，使得表格缩小的时候高度不对。
			// height = _cBWrap.offsetHeight + tableTheaderHeight;
			// 需要设置外围容器高度，否则高度不正确
			$wrap.css({height});
		}
		// bottom
		bottom = horizontal ? scrollbarHeight : 0;
		// 如果有左侧锁定列，那么设置左锁定列css属性
		if (leftColumns && leftColumns.length > 0) {
			_$lWrap && _$lWrap.css({bottom});
		}
		// 如果有右侧锁定列，设置右侧锁定表格css属性
		if (rightColumns && rightColumns.length > 0) {
			right = vertical ? scrollbarWidth : 0;
			_$rWrap && _$rWrap.css({bottom, right});
		}
		// 设置表格容器，该容器包含表头&表体
		_$tableWrapper.css({height});
		// 设置头部表格容器宽度，否则纵向滚动条将导致右上角出现bug
		let headWrapWidth = _cBWrap.scrollWidth === $wrap.width() ? "100%" : _$cBWrap.width();
		_$cHScrollWrap.width(headWrapWidth);
	}

	// 无数据表格处理
	noDataHandler() {

	}

	// 设置表格行数值，此刻需要设置容器高度
	set pageSize(number) {
		this._data.pageSize = number;
		this.resizeHandler();
	}

	// 分页初始化依赖该参数
	get pageSize() {
		let {initData: {pagination}, paginationIns, dataSource, _$tableTitle, $el} = this._data._root._data,
			{_data} = this;
		//
		if (pagination) { //初始化参数有分页
			if (pagination.pageSize) { // 用户自定义pageSize
				// 分页实例存在的时候，直接返回分页实例的pageSize
				if (paginationIns && paginationIns.pageSize && paginationIns.pageSize !== _data.pageSize) {
					_data.pageSize = paginationIns.pageSize;
				} else { // 分页实例不存在，直接调用用户自定义的分页
					_data.pageSize = pagination.pageSize;
				}
			} else if (paginationIns && isNumber(paginationIns.pageSize)) {
				_data.pageSize = paginationIns.pageSize;
			} else {
				_data.pageSize = this.fullScreenPageSize;
			}
		} else { // 显示全部数据，所以pageSize为all
			_data.pageSize = "all";
		}
		return _data.pageSize;
	}

	// 计算满屏pagesize
	get fullScreenPageSize() {
		let {_$tableTitle, $el} = this._data._root._data;
		// 去掉表格表头高度
		let tbodyWrapHeight = $el.height() - tableTheaderHeight;
		// 如果有表格标题，去掉标题高度
		_$tableTitle && (tbodyWrapHeight -= tableTitleHeight);
		// 去掉分页高度
		tbodyWrapHeight -= paginationHeight;
		// 去掉底边边框高度
		tbodyWrapHeight--;
		//
		this._data.pageSize = Math.floor(tbodyWrapHeight / lineHeight);
		// return
		return this._data.pageSize;
	}
}

export default Layout;


