'use strict';
import {isUndefined, isJson, isString} from "./common/util";


// url字符串变量转为json(a=1&b=2&c=3--->{"a":1,"b":2:"c":3})
let queryStringToJsonHandler = function (str) {
	if (isString(str)) {
		if (str.length === 0) return {};
		let args = {};
		let items = str.split('&');
		items.forEach(item => {
			let itemArr = item.split("=");
			args[itemArr[0]] = itemArr[1];
		});
		return args;
	}
	throw new Error(`${str}无法转换为json!`);
}

// 字符串转换为json ("a":1,b:2:c:3--->{"a":1,"b":2:"c":3})
let jsonStringToJsonHandler = function (str) {
	let jsonString = str.toString();
	jsonString = jsonString.indexOf("{") === -1 ? "{" + jsonString + "}" : jsonString;
	return (new Function("return " + jsonString))();
}

/*
* ************************************以上方法不导出************************************************
* */

// 字符串转换为json ("a":1,b:2:c:3--->{"a":1,"b":2:"c":3})
function stringToJson(str) {
	if (isUndefined(str) || str.length === 0) {
		return {};
	} else if (isJson(str)) {
		return str;
	} else if (/:/.test(str)) {
		return jsonStringToJsonHandler(str);
	} else if (/=/.test(str)) {
		return queryStringToJsonHandler(str);
	} else {
		throw new Error(`${str}无法转换为json!`);
	}
}

// json转queryString({"a":1,"b":2}---->a=1&b=2)
const jsonToQueryString = (json={}) => {
	let s = "";
	if (isJson(json)) {
		for (let i in json) {
			s += i + "=" + json[i] + "&";
		}
		s = s.replace(/&$/, "");
		return s;
	} else {
		let log = `json转换query string失败;
----- string start -----
${json}
----- string  end -----`;
		throw new Error(log);
	}
}

export {stringToJson, jsonToQueryString};