'use strict';
import {isJson, isUndefined} from "./util";
import jqIsReady from "../jqIsReady";
import VEvent from "./VEvent";

let config = {};
let instanceArr = [];

class Base extends VEvent {
	constructor() {
		super();
		//
		jqIsReady();
		// console.log("base constructor call;");
		if (isUndefined(this.__data)) {
			this.__data = {
				__customerData: {},
				__customerMethod: {}
			}
		}
	}

	get version() {
		return "0.2.1";
	}

	set config(data) {
		if (isJson(data)) {
			Object.assign(config, data);
			config = data;
		}
	}

	get config() {
		return config;
	}

	// bundle data property
	data(key, value) {
		if (!isUndefined(key)) {
			if (isUndefined(value)) {
				return this.__data.__customerData[key];
			} else {
				this.__data.__customerData[key] = value;
			}
		} else {
			return this.__data.__customerData;
		}
	}

	// bundle custom methods for subclass
	bundleMethods(methods, ...params) {
		let _root = this;
		if (isJson(methods)) {
			for (let name in methods) {
				if (!/^_/.test(name)) {
					this.__data.__customerMethod[name] = methods[name];
					this[name] = function (fun) {
						return (...arg) => {
							let args = arg.concat(params);
							return fun.apply(_root, args);
						}
					}(this.__data.__customerMethod[name]);
				} else {
					throw new Error("自定义方法名字不允许_开头");
				}
			}
		}
	}

	remove() {
		for (let p in this) {
			delete this[p];
		}
	}
}

export default Base;
