'use strict';
import {isEqual, isJson} from "./util";
import browserInfo from "../browserInfo";

const globalData = {
	$body: $(document.body),
	$window: $(window),
	// model property
	set model(val) {
		if (!this._model || isEqual(this._model, {}) || val === null) {
			this._model = val;
		} else {
			throw new Error(`仅能对v.model赋值0或1次, 赋值以后可以修改v.model属性.`);
		}
	},
	get model() {
		this._model = this._model || {};
		return this._model;
	},
	// requireRouter will call this function when execute new module
	resetModel() {
		let _model = this._model;
		for (let p in _model) {
			delete _model[p];
		}
	},
	// import requireJs
	set requireJs(val) {
		this._requireJs = val;
	},
	get requireJs() {
		return this._requireJs;
	},
	// return browser name
	get browserName() {
		this._browserName = this._browserName || browserInfo().name;
		return this._browserName;
	}
}

export default globalData;

