'use strict';

import {isJson, isString} from "./common/util";
import tpl from "./common/tpl";
import _vName from "./componentName";
import html from '../../template/miniPopup.html';
import animationEnd from "./animationEnd";
//
const miniPopupWrapClass = `.${_vName}-miniPopup-modal`,
	miniPopupContClass = `.v-miniPopup-wrap`,
	count = "v-count",
	defaultLoadingText = "成功",
	defaultHideDelaySeconds = 2.5;

// delay hide
const delayHide = ({$target, seconds = defaultHideDelaySeconds}) => {
	let timeoutId = $target.data("v_delayHideTimeOutId");
	clearTimeout(timeoutId);
	$target.data("v_delayHideTimeOutId", setTimeout(() => {
		/*$target.fadeOut(300,() => {
			$target.remove();
		});
*/
		let $targetCont = $target.children(`${miniPopupContClass}`);
		$target.removeClass('vFadeIn');
		$target.addClass('vFadeInOut');
		$targetCont.removeClass('vFadeInDownSmall');
		$targetCont.addClass('vFadeOutDownSmall');
		animationEnd([$target], function () {
			$target.remove();
		});

	}, seconds * 1000));
}

// show createInstance
const createInstance = (ops = defaultLoadingText, iconClass) => {
	let $target = $("body").children(`${miniPopupWrapClass}`);
	//
	if ($target.length === 0) {
		// console.log("==== 0");
		$target = $(tpl(html));
		// 计数
		$target.data(`${count}`, 1);
		// append
		$target.appendTo("body");
	} else {
		$target.data(`${count}`, $target.data(`${count}`) + 1)
	}
	let $textTarget = $target.find(".v-miniPopup-text");
	let $vicon = $target.find(".vicon");
	// 添加样式
	$vicon.attr("class", `vicon ${iconClass}`);
	//
	if (isJson(ops)) {
		let {content = defaultLoadingText, seconds} = ops;
		if (isString(content)) {
			$textTarget.html(content);
		} else if (isFunction(content)) {
			content($textTarget);
		}
		delayHide({$target, seconds});
	}
	// click hide popup success
	$target.on("click", () => {
		hideInstance();
	})
}

// hide popupSuccess
const hideInstance = () => {
	// console.log("hide()");
	let $target = $("body").children(`${miniPopupWrapClass}`);
	let $targetCont = $target.children('.v-miniPopup-wrap');
	// 不记次数，直接隐藏
	/*$target.fadeOut(300,() => {
		$target.remove();
	})*/
	$target.removeClass('vFadeIn');
	$target.addClass('vFadeInOut');
	$targetCont.removeClass('vFadeInDownSmall');
	$targetCont.addClass('vFadeOutDownSmall');
	animationEnd([$target], function () {
		$target.remove();
	});
}

// v.miniPopup.success
const success = content => {
	// 验证参数类型
	validateOptions(content, "success");
	// 图标样式
	let iconClass = "vicon-yes_fill";
	//
	if (isString(content)) {
		content = {content, iconClass}
	} else if (isJson(content)) {
		content.iconClass = iconClass;
	}
	// create instance
	createInstance(content, iconClass);
}
// v.miniPopup.warning
const warning = content => {
	// 验证参数类型
	validateOptions(content, "warning");
	// 图标样式
	let iconClass = "vicon-warning_fill";
	//
	if (isString(content)) {
		content = {content, iconClass}
	} else if (isJson(content)) {
		content.iconClass = iconClass;
	}
	// create instance
	createInstance(content, iconClass);
}
// v.miniPopup.error
const error = content => {
	// 验证参数类型
	validateOptions(content, "error");
	// 图标样式
	let iconClass = "vicon-no_fill";
	//
	if (isString(content)) {
		content = {content, iconClass}
	} else if (isJson(content)) {
		content.iconClass = iconClass;
	}
	// create instance
	createInstance(content, iconClass);
}
// v.miniPopup.info
const info = content => {
	// 验证参数类型
	validateOptions(content, "info");
	let iconClass = "vicon-about_fill";
	if (isString(content)) {
		content = {content, iconClass}
	} else if (isJson(content)) {
		content.iconClass = iconClass;
	}
	// create instance
	createInstance(content, iconClass);
}

// hide
const hideError = hideInstance,
	hideInfo = hideInstance,
	hideSuccess = hideInstance,
	hideWarning = hideInstance;

// validate options
const validateOptions = (ops, type) => {
	if (!isString(ops) && !isJson(ops))
		throw new TypeError(`v.miniPopup.${type}()参数类型错误`);
}


const miniPopup = {
	success, hideSuccess,
	//
	warning, hideWarning,
	//
	error, hideError,
	//
	info, hideInfo
}

// export
export default miniPopup;
