'use strict';

import _cName from "./componentName";

const _n = "postMan";
let v = window[_cName];
let vp = v[_n];

// 执行回调队列
function evalCallback(key, once = 0) {
	let val = vp["_" + key];
	let getCbs = vp["_" + key + "GetCb"] = vp["_" + key + "GetCb"] || [];
	let onCbs = vp["_" + key + "OnCb"] = vp["_" + key + "OnCb"] || [];
	for (let getCb of getCbs) {
		getCb(val);
	}
	if (once === 0) {
		for (let onCb of onCbs) {
			onCb(val);
		}
	}
	delete vp["_" + key + "GetCb"];
}
// 生成回调队列
function addCallback(key, newCb, once = 1) {
	let cbSufix = once === 1 ? "GetCb" : "OnCb";
	vp["_" + key + cbSufix] = vp["_" + key + cbSufix] || [];
	let addEnable = true;
	for (let cb of vp["_" + key + cbSufix]) {
		if (cb === newCb) {
			addEnable = false;
			break;
		}
	}
	// 添加队列
	if (addEnable) {
		vp["_" + key + cbSufix].push(newCb);
	}
}


vp = {
	// 设置元素
	setData(key, val) {
		this["_" + key] = val;
		evalCallback(key);
	},
	// 获得元素
	getData(key, cb) {
		addCallback(key, cb);
		let val = this["_" + key];
		if (typeof(val) !== "undefined") {
			evalCallback(key, 1);
		} else {
			cb(undefined);
		}
	},
	// 监听元素
	onSetData(key, cb) {
		addCallback(key, cb, 0);
	},
	// 删除元素
	deleteData(key) {
		if (typeof(vp["_" + key]) !== "undefined") {
			delete vp["_" + key];
		}
	}
};
export function setData(key, val) {
	vp.setData(key, val);
}
export function getData(key, cb) {
	return vp.getData(key, cb);
}
export function deleteData(key) {
	vp.deleteData(key);
}
export function onSetData(key, cb) {
	vp.onSetData(key, cb);
}
