'use strict';

import {isArray, isFunction, isJq} from "./common/util"

const animationEnd = (target, callback) => {
	if ((isArray(target) || isJq(target)) && isFunction(callback)) {
		let mainDtd = [];
		if (isJq(target)) {
			let arr = [];
			target.each(function () {
				arr.push($(this));
			})
			target = arr;
		}
		target.forEach(($tar) => {
			if (isJq($tar)) {
				if ($tar.length === 1) {
					let dtd = $.Deferred();
					$tar.on("animationend", () => {
						dtd.resolve();
					});
					mainDtd.push(dtd.promise());
				} else if ($tar.length === 0) {
					console.warn(`$("${$tar.selector}")不存在或者不唯一,animationEnd已经忽略这个对象的animationend事件`);
				}
			} else {
				throw new Error("仅能监听jquery对象");
			}
		})
		$.when(...mainDtd).done(function () {
			callback();
		});
	} else {
		throw new Error("animationEnd参数类型错误");
	}
}

export default animationEnd;