'use strict';


const setMainCheckboxTbody = (_root) => {
	// 如果有全选功能，则进行处理
	if (_root._data.tableCheckboxEnable) {
		//
		let {bodyColumns, _$lBTbody, _$cBTbody, _$rBTbody} = _root._data;
		//
		for (let {title, fixed} of bodyColumns) {
			if (title === "table.checkbox") {
				// set tbody that had checkbox
				switch (fixed) {
					case "left":
						_root._data.$checkboxTbody = _$lBTbody;
						break;
					case "right":
						_root._data.$checkboxTbody = _$rBTbody;
						break;
					default:
						_root._data.$checkboxTbody = _$cBTbody;
						break;
				}
			}
		}
	}
}

export default setMainCheckboxTbody;
