// 本地库过期处理(以本地sessionStorage标识为判断标准)
const expiredDBHandler = async (_root, compelled = false) => {
	let {cDB, vDB} = _root._data;
	let expiredDBPromise;
	//
	if (compelled || !sessionStorage.getItem(sessionStorageSymbol)) { // 数据已经过期(session无标识)
		// 清空本地用户库
		let cDBPromise = cDB.clear();
		let vDBPromise = vDB.clear();
		// await localforage.dropInstance({name: cDBName});
		// await localforage.dropInstance({name: vDBName});
		// 保存session
		if (!compelled) {
			let now = new Date().getTime().toString();
			sessionStorage.setItem(sessionStorageSymbol, now);
		}
		// 等待清空
		expiredDBPromise = Promise.all([cDBPromise, vDBPromise]);
	} else {
		expiredDBPromise = Promise.resolve();
	}
	//
	return expiredDBPromise;
}

//
class Storage {
	constructor() {
		// 用户库
		let cDB = localforage.createInstance({
			name: cDBName
		});
		// v使用库
		let vDB = localforage.createInstance({
			name: vDBName
		});
		this._data = {cDB, vDB};
		// 清理本地库
		expiredDBHandler(this);
		// 本地库过期处理
		this._data.expiredDBProcess = expiredDBHandler;
	}

	// 保存
	async setItem(key, val, seconds = defaultExpirationSeconds) {
		let _root = this;
		let {cDB, vDB, expiredDBProcess} = _root._data;
		// 本地库过期处理
		await expiredDBProcess(_root);
		// 保存过期时间参数
		await expiredInfoProcess({key, val, seconds, _root});
		// 保存数据
		return await new Promise((resolve, reject) => {
			try {
				cDB.setItem(key, val).then(() => {
					resolve(val);
				})
			} catch (e) {
				console.error(`v.store.set(${key})失败了`);
				reject(e);
				throw e;
			}
		});
	}

	// 获取某个key值
	async getItem(key, successCallback) {
		let _root = this;
		let {cDB, vDB, expiredDBProcess} = _root._data;
		// 本地库过期处理
		await expiredDBProcess(_root);
		// 已过期数据处理
		await expiredKeyProcess(key, _root);
		// 返回promise
		return await new Promise((resolve, reject) => {
			try {
				let promise = cDB.getItem(key);
				// then
				promise.then(value => {
					// successCallback
					if (isFunction(successCallback)) {
						isFunction(successCallback) && successCallback(value);
					}
					// resolve
					resolve(value);
				})
			} catch (e) {
				reject(e);
			}
		})
	}

	// 移除某个key值
	async removeItem(key) {
		let _root = this;
		let {cDB, vDB, expiredDBProcess} = _root._data;
		// 本地库过期处理
		await expiredDBProcess(_root);
		//
		return await new Promise((resolve, reject) => {
			try {
				let cbRemovePromise = cDB.removeItem(key);
				let vbRemovePromise = vDB.removeItem(key);
				let allPromise = Promise.all([cbRemovePromise, vbRemovePromise]);
				allPromise.then(() => {
					resolve();
				})
			} catch (e) {
				console.error(`v.storage.removeItem(${key})失败`);
				reject(e);
			}
		})
	}

	// 获得离线存储的总的键数
	async length() {
		let _root = this;
		let {cDB, vDB, expiredDBProcess} = _root._data;
		// 本地库过期处理
		await expiredDBProcess(_root);
		//
		return await new Promise((resolve, reject) => {
			try {
				resolve(cDB.length());
			} catch (e) {
				reject(e);
			}
		});
	}

	// 迭代器
	iterate(callback) {
		let _root = this;
		let {cDB, expiredDBProcess} = _root._data;
		// 强制清除本地数据库
		let promise = expiredDBProcess(_root);
		promise.then(() => {
			// callback(value, key, iterationNumber)
			cDB.iterate(callback);
		})
	}

	// 便利key
	async keys() {
		let _root = this;
		let {cDB, vDB, expiredDBProcess} = _root._data;
		// 本地库过期处理
		await expiredDBProcess(_root);
		return await cDB.keys();
	}

	// 清理所有本地数据
	async clear(successCallback) {
		let _root = this;
		let {expiredDBProcess} = _root._data;
		// 强制清除本地数据库
		let promise = await expiredDBProcess(_root, true);
		//
		if (isFunction(successCallback)) {
			promise.then(() => {
				successCallback();
			})
		}
		return promise;
	}

	// 保存
	set(key, val, seconds) {
		return this.setItem(key, val, seconds);
	}

	// 获取某个key值
	get(key, successCallback) {
		return this.getItem(key, successCallback);
	}

	// 移除某个key值
	remove(key) {
		return this.removeItem(key);
	}
}

// storage instance
const storage = new Storage();

// export
export {storage, expiredDBHandler};


