'use strict';


import {isJson, isFunction, isString} from "./common/util";
import Popup from "./Popup";
//
const vAlert = ops => {
	let {confirmHandler} = ops;
	let config = {
		type: "alert",
		title: "确认",
		width: 320,
		height: 180,
		buttons: [
			{
				text: "确认",
				click: function ($wrap, ins) {
					isFunction(confirmHandler) && confirmHandler();
					ins.remove();
				},
				class: "xBtn xBtn-primary xBtn-horpa-lg"
			}
		]
	}
	//
	if (isString(ops)) {
		config.content = ops;
	} else if (isJson(ops)) {
		$.extend(true, config, ops);
	}
	//
	return new Popup(config);
}
//
export default vAlert;
