'use strict';
import isNumber from "lodash/isNumber";
import isString from "lodash/isString";
import isBoolean from "lodash/isBoolean";
import _isEqual from "lodash/isEqual";
import isElement from "lodash/isElement";
// import $ from 'expose-loader?$!jquery';
// import isArray from "lodash/isArray";
// import isFunction from "lodash/isFunction";
// import _equalArrays from "lodash/_equalArrays";
// import _equalObjects from "lodash/_equalObjects";
// import once from "lodash/once";

// 判断jquery对象
const isJq = function (obj) {
	return !isUndefined(obj) && obj instanceof $;
}

const isNull = value => {
	return value === null;
}

const isUndefined = value => {
	return value === undefined;
}

const isEqual = (obj1, obj2) => {
	// if(isArray(obj1)&&isArray(obj2)){
	// 	return _equalArrays(obj1,obj2);
	// }else if(typeof(obj1)==="object"&&typeof(obj2)==="object"){
	// 	// return _equalObjects(obj1,obj2);
	// 	return false;
	// }
	return _isEqual(obj1, obj2);
}

// 判断json对象
const isJson = function (data) {
	return typeof(data) === "object" && Object.prototype.toString.call(data).toLowerCase() === "[object object]" && !data.length;
};

// run once
const once = (() => {
	let n = 1;
	return (func, ...args) => {
		let result;
		if (n > 0) {
			result = func.apply(this, args);
			n--;
		}
		return result;
	}
})();

// isDetached
const isDetached = $dom => {
	return $dom.closest("body").length === 0;
}

// 判断es5
// const isES5 = (function(){
