// })();

// let isString = function (str) {
// 	return (typeof str === 'string') && str.constructor === String;
// }

// 获得对象长度
const getLength = function (obj = {}) {
	let count = 0;
	for (let i in obj) count++;
	return count;
}

const isArray = (obj) => {
	return obj && $.isArray(obj);
}

const isFunction = (obj) => {
	return obj && $.isFunction(obj);
}


/*
function test(a, b) {
	console.log("test()");
	console.log(a, b);
	console.log(this);
}
createTable();
createTable();
createTable();
var createTable = runOnce(test, [111, 222], {a: 1, b: 2});
*/
const runOnce = (fn, arg, context) => {
	return function () {
		let result;
		try {
			isFunction(fn) && (result = fn.apply(context || this, arg));
		} catch (e) {
			throw e;
		} finally {
			fn = null;
		}
		return result;
	}
}

const isObject = (target) => {
	return target != null && typeof target === 'object' && Array.isArray(target) === false;
}

