'use strict';

import Base from "./common/Base";
import {isFunction, isUndefined, isJson, isArray, isNumber, isString} from "./common/util";
import tpl from "./common/tpl";
import html from '../../template/tab.html';
import loadBodyHtml from "./common/loadBodyHtml";
//
const tabClass = "tab-li", tabCurr = "tab-curr";

// create wrap for new tab
let getTabWrap = () => {
	let $title = $(`<li class='${tabClass}'></li>`);
	let $content = $(`<div class='tab-inner'></div>`);
	return {$title, $content}
}

// set default data for tabs
let setDefaultData = (_root) => {
	_root._data = {
		tabs: [],
		createdCallback: [],
		isOver: false
	}
}

// set actived tab
let setDefaultActivedTab = (_root, activedTab) => {
	if (activedTab) {
		_root._data.activedTab = activedTab;
	} else {
		_root.active(1);
	}
}

// focusinHandler
let focusinHandler = (piece) => {
	let {$title, $content, focusin, title, _root} = piece._data;
	let activedTab = _root._data.activedTab;
	$title.addClass(tabCurr);
	$content.show();
	if (isUndefined(activedTab)) {
		// activate new tab
		setDefaultActivedTab(_root, piece);
		if (isFunction(focusin)) {
			focusin.apply(piece, [$content, piece]);
		}
	} else if (activedTab._data.title !== title) {
		setDefaultActivedTab(_root, piece);
		focusoutHandler(activedTab);
		if (isFunction(focusin)) {
			focusin.apply(piece, [$content, piece]);
		}
	}
}

// focusoutHandler
let focusoutHandler = (piece) => {
	let {$title, $content, title, focusout, _root} = piece._data;
	let activedTab = _root._data.activedTab;
	if (!isUndefined(activedTab) && activedTab.title !== title) {
		if (isFunction(focusout)) {
			focusout.apply(piece, [$content, piece]);
		}
		$title.removeClass(tabCurr);
		$content.hide();
	}
}

// clickHandler
let clickHandler = (piece) => {
	let {$content, click} = piece._data;
	// activate new tab
	if (isFunction(click)) {
		click.apply(piece, [$content, piece]);
	}
}

// init
let init = ({_root, $wrapper}) => {
	// get template for tab component
	let $dom = $(tpl(html));
	// empty container
	$wrapper.empty();
	// append dom to container
	$dom.appendTo($wrapper);
	// save $dom
	_root._data.$dom = $wrapper;
	// save tab header container
	_root._data.$header = $dom.find(".tab-ul");
	// save tab body container
	_root._data.$body = $dom.find(".tab-content");
}

const createdCallbackHandler = (_root) => {
	let {createdCallback, isOver} = _root._data;
	if (isOver) {
		try {
			for (let cb of createdCallback) {
				if (isFunction(cb)) {
					cb(_root);
				}
			}
			_root._data.createdCallback = null;
		} catch (e) {
			console.warn("created回调错误");
		}
	}
}

// -------初始化方法-------
// focusin(仅激活当前标签的时候执行,再次点击该标签不执行)
// focusout(离开当前tab时执行)
// click(点击tab标签时执行)
// onPrevClick(prev 按钮被点击的回调)
// onNextClick(next 按钮被点击的回调)

// -------初始化属性-------
// default 默认激活的标签

// -------实例属性-------
// activedTab(当前激活的tab)
// ------- 暂时为实现 -------
// type(页签的基本样式，可选 line、card editable-card 类型)
// 类
class Tab extends Base {
	constructor(data) {
		super();
		//
		let {tabs, el, method, created} = data;
		let _root = this, $wrapper = $(el);
		setDefaultData(_root);
		_root._data.def = new $.Deferred();
		init({_root, tabs, $wrapper});
		super.bundleMethods(method, _root);
		_root._data.$wrapper = $wrapper;
		if (isArray(tabs) && tabs.length > 0) {
			let mainDef = $.Deferred(),
				defArr = [];
			// 生成tab
			for (let tab of tabs) {
				defArr.push(_root.addTab(tab, mainDef));
			}
			// call created after html loaded
			$.when(...defArr).done(() => {
				isFunction(created) && created(_root);
				_root._data.isOver = true;
				createdCallbackHandler(_root);
			})
			setDefaultActivedTab(_root, _root._data.activedTab);
		}
	}

	set onCreated(cb) {
		this._data.createdCallback.push(cb);
		createdCallbackHandler(this);
	}

	// remove
	remove() {
		for (let item of this._data.tabs) {
			item.remove();
		}
		this._data.$wrapper.empty();
		this._data = null;
		this.__data = null;
	}

	// 重新加载某个标签的内容
	tabRefresh() {
	}

	// 激活某个标签
	active(target) {
		if (target) {
			let t;
			target = Number(target);
			if (isNumber(target)) {
				t = this._data.tabs[target - 1];
			} else if (target instanceof Piece) {
				t = target;
			} else if (isString(target)) {
				for (let item of this._data.tabs) {
					if (item.title === target) {
						t = item;
						break;
					}
				}
			}
			focusinHandler(t);
		} else {
			throw new Error(`tab.active()参数错误, ${target}`);
		}
	}

	getTab(target) {
		let t;
		if (isNumber(target)) {
			t = this._data.tabs[Number(target) - 1];
		} else if (isString(target)) {
			for (let item of this._data.tabs) {
				if (item.title === target) {
					t = item;
					break;
				}
			}
		}
		return t;
	}

	addTab(tabData) {
		let def = $.Deferred();
		if (isJson(tabData)) {
			let newTab = new Piece(this, tabData, def);
			this._data.tabs.push(newTab)
		} else {
			throw new Error("addTab()参数错误");
		}
		return def.promise();
	}

	get length() {
		return this._data.tabs.length;
	}

	get tabs() {
		return this._data.tabs;
	}

	get activedTab() {
		return this._data.activedTab;
	}

	get $dom() {
		return this._data.$wrapper;
	}
}

class Piece extends Base {
	constructor(_root, tabData, def) {
		super();
		let _tab = this;
		let {$title, $content} = getTabWrap();
		let {title, text, content, focusin, click, focusout, url, data, method} = tabData;
		// 向下兼容!!! 已经有项目(医生管理-yd)在使用text属性.
		title = isUndefined(title) ? text : title;
		//set default data
		_tab._data = {
			$title, $content, focusin, focusout, click,
			title, content, _root, method, customMethod: {}
		};
		super.bundleMethods(method, $content, _tab);
		// get data
		let {$header, $body, activedTab} = _root._data;
		// save active tab class  && current tab
		if (tabData.default === true) {
			$title.addClass(tabCurr);
			if (!isUndefined(activedTab)) {
				focusoutHandler(activedTab);
			}
			setDefaultActivedTab(_root, this);
		} else {
			$content.hide();
		}
		// add title for tab
		$title.html(title);
		if (isFunction(content)) {
			// get page
			if (isString(url)) {
				let page = {url, data};
				loadBodyHtml(page).then($bodyChildrenHtml => {
					$content.empty();
					$bodyChildrenHtml.appendTo($content);
					content.apply(_root, [$content, _tab, _root]);
					def && def.resolve();
				})
			} else {
				content.apply(_root, [$content, _tab, _root]);
				def && def.resolve();
			}
		} else if (isString(content)) {
			$content.html(content);
		}
		// bind click event
		$title.on("click", function () {
			focusinHandler(_tab);
			clickHandler(_tab);
		});
		// 添加进tabs容器
		$title.appendTo($header);
		$content.appendTo($body);
	}

	getTab(i) {
		let {_root} = this._data;
		return _root.getTab(i);
	}

	remove() {
		let {$title, $content} = this._data;
		$title.off().remove();
		$content.off().remove();
	}

	get title() {
		return this._data.title;
	}

	get $title() {
		return this._data.$title;
	}

	get $content() {
		return this._data.$content;
	}

	get activedTab() {
		return this._data._root._data.activedTab
	}

	get tabs() {
		return this._data._root;
	}
}


export default Tab;
