'use strict';


// 组件名字
import _cName from "./componentName";
import Base from "./common/Base";
// 模版
import {drag} from "./func";
import {isUndefined, isFunction, isArray, isNumber} from "./common/util";
import globalData from "./common/globalData";
import tpl from "./common/tpl";
import _vName from "./componentName";
import html from '../../template/popup.html';
import loadBodyHtml from "./common/loadBodyHtml";
import animationEnd from "./animationEnd";

// 默认参数
const defaultWidth = "50%",
	defaultHeight = "400px",
	// 模态窗口class
	modalDivClass = ".popup-modaldrop",
	// 模态窗口background值
	modalBgCss = "rgba(0,0,0,.4)",
	dragStartClass = `${_vName}-popup-wrap-dragStart`;

// 添加dom
const appendDomValue = (root, data) => {
	const template = $(tpl(html)),
		// popup容器
		popupWrapClass = "." + _cName + "-popup-wrap",
		// 按钮容器class
		btnWrapClass = ".popup-btns",
		// 内容容器class
		conWrapClass = ".popup-content",
		// 左下角文本class
		footerTextClass = ".popup-footerText",
		// 左下角文本class
		footerWrapClass = ".popup-footerWrap",
		// 关闭按钮class
		closeBtnClass = ".popup-close",
		// 关闭按钮class
		titleClass = ".popup-title";
	//默认模态窗口
	let {modal = true} = data;
	let _data = root._data;
	_data._$wrap = modal ? $(tpl(html)) : $(tpl(html).html());
	_data.$popupWrap = modal ? _data._$wrap.find(popupWrapClass) : _data._$wrap;
	_data.$btnWrap = _data._$wrap.find(btnWrapClass);
	_data.$conWrap = _data._$wrap.find(conWrapClass);
	_data.$titleWrap = _data._$wrap.find(titleClass);
	_data.$footerWrap = _data._$wrap.find(footerWrapClass);
	_data.$modalDiv = _data._$wrap.filter(modalDivClass);
	//
	let $closeBtn = _data._$wrap.find(closeBtnClass);
	let $footerText = _data._$wrap.find(footerTextClass);
	// hide close btn
	data.hideCloseBtn !== true ? _data.$closeBtn = $closeBtn : $closeBtn.remove();
	//
	// confirm || alert
	if (data.type === "confirm" || data.type === "alert") {
		$footerText.remove();
		$closeBtn.remove();
		_data.$popupWrap.addClass("v02-popupConfirm-wrap");
	} else {
		_data.$footerText = $footerText;
	}
}

const appendOtherValue = (root, data) => {
	let {closeCallback, width = defaultWidth, height = defaultHeight, modal = true, id} = data;
	let percentReg = /^\d+%$/;
	let pxReg = /^\d+px$/;
	if (percentReg.test(width)) {
		width = Number(width.replace(/%$/, "")) / 100;
	} else if (pxReg.test(width)) {
		width = Number(width.replace(/px$/, ""));
	}
	if (percentReg.test(height)) {
		height = Number(height.replace(/%$/, "")) / 100;
	} else if (pxReg.test(height)) {
		height = Number(height.replace(/px$/, ""));
	}
	// 保存高宽尺寸
	root._data._width = width;
	root._data._height = height;
	// 关闭回调
	root._data.closeCallback = closeCallback;
	root._data._modal = modal;
	if (typeof(id) === "string") {
		saveInstance(id, root);
	}
}

const bindEvent = (root, data) => {
	let {modalCloseable} = data;
	let {$closeBtn, $modalDiv, $popupWrap} = root._data;
	// 关闭事件
	$closeBtn && $closeBtn.on("click", e => {
		root.remove();
	});
	// 点击模态背景关闭事件
	modalCloseable && $modalDiv.on("click", function (e) {
		if (e.target === this) {
			root.remove();
		}
	});
	// 拖动事件
	root._data.dragInstance = drag({
		target: $popupWrap,
		handleClass: ".popup-header",
		dragStart(x, y) {
			$popupWrap.addClass(dragStartClass);
		},
		dragEnd(x, y) {
			$popupWrap.removeClass(dragStartClass);
		}
	})
}

// 设置容器
const setWrap = (root, data) => {
	let {$popupWrap, $titleWrap, _width, _height} = root._data;
	let {title = ""} = data;
	// 设置尺寸
	$popupWrap.width(_width);
	$popupWrap.height(_height);
	$titleWrap.html(title);
}

// 生成按钮
const addBtns = (root, btns) => {
	let {$btnWrap, $conWrap} = root._data;
	$btnWrap.children().remove();
	for (let btn of btns) {
		let btnClass = btn.class ? `class="${btn.class}"` : "";
		let $btn = $(`<button type="button" ${btnClass}>${btn.text}</button>`);
		let cb = btn.click;
		$btn.on("click", e => {
			if (isFunction(cb)) {
				cb.apply(root, [$conWrap, root]);
			} else {
				// 无click方法,点击就删除popup
				root.remove();
			}
		})
		$btn.appendTo($btnWrap);
	}
}

// 居中处理
const center = (root) => {
	let {$popupWrap, _width, _height} = root._data;
	let $window = globalData.$window;
	let pixelReg = /^[2-9]|^[1-9]\d+/;
	// 改变窗口尺寸处理
	let resizeHandler = () => {
		let gap = 20, nw, nh, left, top;
		let ww = document.body.clientWidth;
		let wh = document.body.clientHeight;
		// width
		if (pixelReg.test(_width)) {
			// number pixel for example 300
			nw = _width;
		} else if (isNumber(_width) && _width <= 1 && _width > 0) {
			// percent for example 0.3
			nw = _width * ww;
		}
		// height
		if (pixelReg.test(_height)) {
			// number pixel for example 300
			nh = _height;
		} else if (isNumber(_height) && _height <= 1 && _height > 0) {
			// percent for example 0.3
			nh = _height * wh;
		}
		// correct size and position
		if (_width === 1 && _height === 1) {
			nw = nh = "100%";
			left = top = 0;
		} else {
			// size
			nw = nw > ww ? ww - gap : nw;
			nh = nh > wh ? wh - gap : nh;
			// floor
			nw = Math.floor(nw);
			nh = Math.floor(nh);
			// positon
			left = Math.floor((ww - nw) / 2);
			top = Math.floor((wh - nh) / 2);
		}

		// set css
		$popupWrap.css({
			width: nw,
			height: nh,
			left: left,
			top: top
		})
	}
	$window.on("resize.v-Popup", resizeHandler);

	resizeHandler();
}

//初始化
const init = function (root, data) {
	let _data = root._data;
	let {content, buttons, footerText} = data;
	let {$conWrap, $footerWrap, $footerText} = _data;
	// footerText
	if (!isUndefined(footerText) || !isUndefined(buttons)) {
		$footerWrap.css("display", $footerWrap.data("display"));
		// 生成按钮内容
		if (isArray(buttons)) {
			addBtns(root, buttons);
		}
		// 生成左下角文本
		if (typeof(footerText) === "string") {
			$footerText.empty();
			$footerText.html(footerText);
		} else if (isFunction(footerText)) {
			$footerText.empty();
			footerText.apply(root, [$footerText, root]);
		}
	} else {
		if (isUndefined($footerWrap.data("display")))
			$footerWrap.data("display", $footerWrap.css("display"));
		$footerWrap.hide();
	}
	// 添加内容
	if (isFunction(content)) {
		content.apply(root, [$conWrap, root])
	} else if (typeof(content) === "string") {
		$conWrap.html(content);
	}
	// if iframe then postmessage
	if (window.self !== window.top) {
		window.top.postMessage("vFullScreenView", "*");
	}
}

const hideModalDiv = (root) => {
	let {_modal} = root._data;
	let $body = globalData.$body;
	let $modalDiv = $body.children(modalDivClass);
	if (_modal && $modalDiv.length > 1) {
		let $target = $modalDiv.not(":last");
		$target.css("background", "none");
	}
}

const showModalDiv = () => {
	let $target = $("body").children(modalDivClass).last();
	$target.css("background", modalBgCss);
}

const saveInstance = (id, root) => {
	if (isUndefined(Popup.popupInstance)) {
		Popup.popupInstance = {};
	}
	Popup.popupInstance[id] = root;
}
const deleteInstance = (id) => {
	if (typeof(id) === "string")
		delete Popup.popupInstance.id;
}


// {
// title = "",width = defaultWidth,height = defaultHeight,content = "",
// footerText,buttons,closeCallback,modal = true,modalCloseable = false
// }
class Popup extends Base {
	constructor(data) {
		super();
		//
		// 生成对象
		let root = this;
		// 保存变量
		this._data = {};
		// 创建弹窗
		appendDomValue(root, data);
		// 创建其他变量
		appendOtherValue(root, data);
		// 设置各个容器
		setWrap(root, data);
		// 绑定事件
		bindEvent(root, data);
		let {$conWrap, _$wrap, $popupWrap, $footerText, $closeBtn} = root._data;
		// 绑定自定义方法
		super.bundleMethods(data.method, this);
		// 清空内容容器
		$conWrap.empty();
		//
		if (data.url) {
			let loadHtmlData = {
				url: data.url,
				data: data.data || {},
				cache: data.cache || true,
				type: data.type || "GET",
			}
			//
			loadBodyHtml(loadHtmlData).then($bodyChildrenHtml => {
				if (root._data) {
					$bodyChildrenHtml.appendTo($conWrap);
					init(root, data);
				}
			})
		} else {
			// 初始化
			init(root, data);
		}
		// 添加进页面
		_$wrap.appendTo($("body"));
		// 隐藏更多的modalDiv背景
		hideModalDiv(root);
		//居中处理
		center(root);
	}

	remove(cbEnable) {
		let {_$wrap, closeCallback, $conWrap, $popupWrap, id} = this._data;
		let $window = globalData.$window;

		// 移除 动画
		let removeAnimation = () => {
			_$wrap.removeClass('vFadeIn');
			_$wrap.addClass('vFadeOut');
			$popupWrap.removeClass('vAniPopIn');
			$popupWrap.addClass('vAniPopOut');

			animationEnd([_$wrap, $popupWrap], function () {
				removeFunc();
			});
			//removeFunc();
			/*$popupWrap.removeClass('vFadeInDown');
			$popupWrap.removeClass('faster');
			$popupWrap.addClass('vFadeOutDown');
			$popupWrap.addClass('fastest');
			animationEnd([$popupWrap], function () {
				removeFunc();
			});*/
		}
		let removeFunc = () => {
			_$wrap.find("*").off();
			_$wrap.remove();
			this._data = null;
			super.remove();
			deleteInstance(id);
			showModalDiv();
		}

		if (cbEnable !== true) {
			if (isFunction(closeCallback)) {
				let cb = closeCallback.apply(this, [$conWrap, this]);
				if (cb !== false) {
					$window.off("resize.v-Popup");
					// 移除 动画
					removeAnimation();
				}
			} else {
				$window.off("resize.v-Popup");
				// 移除 动画
				removeAnimation();
				/*_$wrap.find("*").off();
				_$wrap.remove();
				this._data = null;
				super.remove();
				deleteInstance(id);
				showModalDiv();*/
			}
		} else {
			// 移除 动画
			removeAnimation();
			/*_$wrap.find("*").off();
			_$wrap.remove();
			this._data = null;
			super.remove();
			deleteInstance(id);
			showModalDiv();*/
		}
		// if iframe then postmessage
		if (window.self !== window.top) {
			window.top.postMessage("vDefaultView", "*");
		}
	}


	set content(content) {
		let {$conWrap} = this._data;
		// 添加内容
		if (isFunction(content)) {
			content.apply(this, [$conWrap])
		} else if (typeof(content) === "string" || "number") {
			$conWrap.html(content)
		}
	}
}

export default Popup;
