'use strict';

import onResize from "../common/onResize";

const calculateColWidth = (width, columns) => {
	// min table width
	let minWidth = 0, scaleCount = 0, freeWidth = 0, piece = 0, bodyCols = "", widthArr = [],
		columnsArrLength = columns.length;
	// calculate width and scale
	columns.forEach(item => {
		minWidth += item.width;
		scaleCount += item.scale;
		widthArr.push(item.width);
	});
	// free width
	freeWidth = width - minWidth;
	if (freeWidth > 0) {
		piece = freeWidth / scaleCount;
		// create cols
		for (let i = 0; i < columnsArrLength; i++) {
			if (i < columnsArrLength - 1) {
				let addWidth = Math.floor(columns[i].scale * piece);
				widthArr[i] += addWidth;
				freeWidth -= addWidth;
			} else {
				widthArr[i] += freeWidth;
			}
		}
	}
	//
	for (let item of widthArr) {
		bodyCols += `<col style = "width:${item}px; min-width:${item}px; max-width:${item}px;">`;
		// bodyCols += `<col style = "width:${item}px;">`;
	}
	// return
	return {widthArr, bodyCols, minWidth};
}

// set Colgroup
const createCols = _root => {
	let {
		_$cHColgroup, _$cBColgroup, _$lHColgroup, _$lBColgroup, _$rHColgroup, _$rBColgroup,
		_$cBWrap, _$lWrap, _$rWrap,
		_$lHTable, _$lBTable, _$cHTable, _$cBTable, _$rHTable, _$rBTable, scrollbarWidth
	} = _root._data;
	//get cols
	let {leftColumns, bodyColumns, rightColumns, $el} = _root._data;
	// el width
	let clientWidth = _$cBWrap[0].clientWidth;
	let totalWidth = Math.floor(clientWidth === 0 ? $el.width() : clientWidth);
	//
	let {widthArr, bodyCols, minWidth} = calculateColWidth(totalWidth - 1, bodyColumns);
	//
	if ($el.width() > minWidth + scrollbarWidth) {
		_$cBWrap.css("overflow-x", "hidden");
	} else {
		_$cBWrap.css("overflow-x", "auto");
	}
	//
	// if (_$lHTable && _$lBTable) {
	// }
	if (_$cHTable && _$cBTable) {
		_$cHTable.css({minWidth});
		_$cBTable.css({minWidth});
		//center colgroup
		_$cHColgroup.html(bodyCols);
		_$cBColgroup.html(bodyCols);
	}
	// left columns
	if (leftColumns) {
		_$lHTable.css({minWidth});
		_$lBTable.css({minWidth});
		// left colgroup
		_$lHColgroup.html(bodyCols);
		_$lBColgroup.html(bodyCols);
		//
		let columnsArr = [], currWrapWidth = 0;
		// calculate left wrap width
		for (let i = 0; i < leftColumns.length; i++) {
			let w = widthArr[i];
			columnsArr.push(w);
			currWrapWidth += (w);
		}
		// set width
		_$lWrap.width(currWrapWidth + 1);
	}
	// right columns
	if (rightColumns) {
		_$rHTable.css({minWidth});
		_$rBTable.css({minWidth});
		// right html
		_$rHColgroup.html(bodyCols);
		_$rBColgroup.html(bodyCols);
		// calculate right wrap width
		let columnsArr = [], currWrapWidth = 0;
		// calculate left wrap width
		for (let i = rightColumns.length; i > 0; i--) {
			let w = widthArr[widthArr.length - i];
			columnsArr.push(w);
			currWrapWidth += (w);
		}
		// set width
		_$rWrap.width(currWrapWidth + 1);
	}
}

class Colgroup {
	constructor(_root) {
		let {$wrap} = _root._data;
		this._data = {_root};
		createCols(_root);
		// onResize
		onResize($wrap, () => {
			// 尺寸改变处理
			createCols(_root);
			// 延迟处理
			// delayResize()
		})
		createCols(_root);
	}

	resize() {
		let {_root} = this._data;
		createCols(_root);
	}
}

export default Colgroup;


