'use strict';

import {isJson, isJq, isString} from "./common/util";
import tpl from "./common/tpl";
import _vName from "./componentName";
import html from '../../template/loading.html';
import animationEnd from "./animationEnd";
//
const loadingWrapClass = `.${_vName}-loading-modal`,
	loadingContClass = `.v-loading-wrap`,
	count = "v-count",
	defaultLoadingText = "加载中...";

// show loading
const showLoading = (ops = defaultLoadingText) => {
	let $target = $("body").children(`${loadingWrapClass}`);
	//
	if ($target.length === 0) {
		$target = $(tpl(html));
		// 计数
		$target.data(`${count}`, 1);
		// append
		$target.appendTo("body");
	} else {
		$target.data(`${count}`, $target.data(`${count}`) + 1)
	}
	let $textTarget = $target.find(".v-loading-text");
	// set loading
	if (isString(ops)) {
		$textTarget.html(ops);
	} else {
		throw new TypeError("v.showLoading()参数类型错误");
	}
	// return
	return {$target};
}

// hide loading
const hideLoading = ops => {
	// console.log("hideLoading()");
	let $target = $("body").children(`${loadingWrapClass}`);
	if ($target.data(`${count}`) === 1) {
		/*$target.fadeOut(300, () => {
			$target.remove();
		})*/
		let $targetCont = $target.children(`${loadingContClass}`);
		$target.removeClass('vFadeIn');
		$target.addClass('vFadeInOut');
		$targetCont.removeClass('vFadeInDownSmall');
		$targetCont.addClass('vFadeOutDownSmall');
		animationEnd([$target], function () {
			$target.remove();
		});
	} else {
		$target.data(`${count}`, $target.data(`${count}`) - 1);
	}
}

// export
export {showLoading, hideLoading};
