'use strict';

import Base from "../common/Base";
import createPagination from "./createPagination";

const minShowloadingSeconds = .7,
	tableLoadingClass = "v-table-isLoading",
	tooltipTextAttribute = "data-v-tooltip";

const freshHandler = (_root) => {
	let {_$footerRefresh} = _root._data;
	//
	_$footerRefresh.on("click", () => {
		if (!_$footerRefresh.hasClass(tableLoadingClass)) {
			_root.refresh();
		}
	})
}

// 实例化的时候，一定有dataSource属性
class Footer extends Base {
	constructor(_root) {
		super();
		this._data = {_root};
	}

	init() {
		let {
			_root, _root: {
				_data: {initData: {pagination}, _$footerRefresh}
			}
		} = this._data;
		// create pagination instance once when pagination is not null
		createPagination(this);
		// 右下角刷新按钮初始化
		freshHandler(_root);
	}

	showLoading() {
		let {
			_data: {_$footerLoading}
		} = this._data._root;
		//
		this._data.loadingTime = new Date().getTime();
		_$footerLoading.addClass(tableLoadingClass);
		_$footerLoading.attr(tooltipTextAttribute, "正在加载数据...")
	}

	hideLoading() {
		let {
			_root: {_data: {_$footerLoading}},
			loadingTime
		} = this._data;
		let refreshTooltipTxt = "点击刷新表格";
		// 修改显示文本
		let now = new Date().getTime();
		if (now - loadingTime > minShowloadingSeconds * 1000) {
			_$footerLoading.removeClass(tableLoadingClass);
			_$footerLoading.attr(tooltipTextAttribute, refreshTooltipTxt)
		} else {
			setTimeout(() => {
				_$footerLoading.removeClass(tableLoadingClass);
				_$footerLoading.attr(tooltipTextAttribute, refreshTooltipTxt)
			}, minShowloadingSeconds * 1000);
		}
	}

	orderInfo(txt) {
		let {
			_data: {_$footerOrder}
		} = this._data._root;
		_$footerOrder.stop(true, true).text(txt).show();
		setTimeout(() => {
			_$footerOrder.fadeOut(() => {
				_$footerOrder.text("");
			});
		}, 1000);
	}

	// 无分页处理
	noPagination() {
		let {
			_data: {_$footerWrap}
		} = this._data._root;
		_$footerWrap.remove();
	}
}

export default Footer;
