'use strict';
import Popup from "./Popup";
import {isUndefined} from "./common/util";

let getPopupInstance = (id) => {
	if (isUndefined(Popup.popupInstance)) {
		Popup.popupInstance = {};
	}
	let temp = Popup.popupInstance[id];
	if (isUndefined(temp)) {
		return temp;
	} else {
		return temp._data ? temp : undefined;
	}
}

export default getPopupInstance;