// instance
// class configure once
// configure default router

const router = {
	get rInstance() {
		if (!AmdRouter._instance) {
			throw new Error("获取路由实例错误");
		}
		return AmdRouter._instance;
	},
	get module() {
		return this.rInstance.module;
	},
	get realModule() {
		return this.rInstance.realModule;
	},
	get moduleArr() {
		return this.rInstance.moduleArr;
	},
	get params() {
		return this.rInstance.params;
	},
	get model() {
		return this.rInstance.model;
	},
	submodule,
	paramsChange: function (data) {
		this.rInstance.paramsChange(data);
	},
	setParams(data, set) {
		return this.rInstance.setParams(data, set);
	},
	goto: function (data) {
		this.rInstance.goto(data);
	},
	// 初始化
	init: function (requireJs) {
		this.rInstance.init(requireJs);
		return this;
	}
	// deleteParams(data) {
	// 	return this.rInstance.deleteParams(data);
	// },
	// 以下属性不在router内导出,已经全局导出了.
	// // globalModel
	// set globalModel(value) {
	// 	globalData.globalModel = value;
	// },
	// // globalModel
	// get globalModel() {
	// 	return globalData.globalModel;
	// },
};

// seal
Object.seal(router);

export default router;