/**
 * @authors 12687185@qq.com
 * @date    2017/11/17/0017 16:32:04
 * parse column by _root._data._customerData
 */
'use strict';


const parseColumns = _root => {
	let {columns} = _root._data.initData;
	let leftColumns = columns.filter(item => item.fixed === "left" && item.hidden !== true),
		rightColumns = columns.filter(item => item.fixed === "right" && item.hidden !== true),
		// 中间列 **************************************************
		centerColumns = columns.filter(item => item.fixed !== "left" && item.fixed !== "right" && item.hidden !== true),
		bodyColumns = leftColumns.concat(centerColumns, rightColumns),
		tableCheckboxEnable = false;
	// 左中右列数 **************************************************
	//
	leftColumns = leftColumns.length > 0 ? leftColumns : false;
	rightColumns = rightColumns.length > 0 ? rightColumns : false;
	//
	for (let item of bodyColumns) {
		if (item.render === "table.checkbox") {
			tableCheckboxEnable = true;
			break;
		}
	}
	//
	$.extend(true, _root._data, {
		leftColumns, rightColumns, bodyColumns, tableCheckboxEnable
	});
	// return
	return {leftColumns, rightColumns, bodyColumns, tableCheckboxEnable};
}

export default parseColumns;
