// 加载单个页面
const singlePageLoader = (options, callback, def) => {
	let {url, data = {}, success} = options;
	let loader = $.ajax(options);
	loader.then(html => {
		isFunction(success) && success($(html));
		isFunction(callback) && callback($(html));
		def.resolve($(html));
	}, (XMLHttpRequest, textStatus, thrownError) => {
		throw new Error(`加载${url}失败`);
	})
}

const loadHtml = (options, callback) => {
	try {
		jqIsReady();
		let def = $.Deferred();
		if (isString(options)) { // 参数是字符串, 当前参数当做url
			singlePageLoader({url: options}, callback, def);
		} else if (isJson(options)) {
			if (options.url) { // 只有一个页面,有可能有其他的参数(data,type...)
				singlePageLoader(options, callback, def);
			}
			// else { //按照多个页面进行解析
			// 	// multiplePageLoader(options, callback, def);
			// }
		}
		return def.promise();
	} catch (e) {
		throw new Error("loadHtml执行失败");
	}
}

export default loadHtml;