'use strict';

import Base from "./common/Base";
import {isNumber, isArray, isJson, isUndefined, isFunction} from "./common/util";
import jqIsReady from "./jqIsReady";
import tpl from "./common/tpl";
import html from '../../template/pagination.html';
import _vName from "./componentName";


// 按钮禁用样式
const disableClass = "pagination-disabled",
	// 输入框
	inputClass = ".pagination-input",
	// go btn
	goBtnClass = ".pagination-go",
	// options
	pageSizeChangerWrapClass = `.${_vName}-pagination-options`,
	quickJumperWrapClass = `.${_vName}-pagination-options-quick-jumper`,
	// 上一页按钮样式
	prevClass = ".pagination-prev",
	// 下一页按钮样式
	nextClass = ".pagination-next",
	// options
	opsItemClass = "li",
	optionsItemWrapClass = ".select-dropdown-menu",
	opsListWrapClass = ".select-dropdown",
	pageSizeNumClass = ".pagination-options-pagesize",
	// opstion被选中样式
	itemSelectedClass = "selected",
	// 数字容器
	numWrapClass = ".v-pagination-num-wrap",
	numSelectedClass = "pagination-item-active",
	// 数字选择器
	numClass = `li:not('.pagination-jump-prev'):not('.pagination-jump-next'):not('${nextClass}'):not('${prevClass}')`;

// 保存dom数据
const appendDomValue = (root, data) => {
	let {el} = data;
	let _$wrapper = $(el);
	//判断容器
	if (_$wrapper.length !== 1) {
		throw new TypeError("分页容器错误;");
	}
	// dom
	let _$dom = $(tpl(html));
	// 数字按钮容器
	let _$numWrap = _$dom.find(numWrapClass);
	// 第一页按钮
	let _$prev = _$dom.find(prevClass);
	// 最后一页按钮
	let _$next = _$dom.find(nextClass);
	// input
	let _$input = _$dom.find(inputClass);
	// goBtn
	let _$goBtn = _$dom.find(goBtnClass);
	let _$options = _$dom.find(pageSizeChangerWrapClass);
	let _$optionsList = _$options.find(opsListWrapClass);
	let _$pageSizeNumWrap = _$dom.find(pageSizeNumClass);
	// 返回
	root._data = {
		_$dom, _$wrapper, _$numWrap, _$prev, _$next, _$input,
		_$goBtn, _$options, _$optionsList, _$pageSizeNumWrap
	};
}

// 保存其他数据
const appendOtherValue = (root, data) => {
	let {
		total, pageSize, pageNumber = 1, pageSizeOptions,
		onPageNumberChange, showPageSizeChanger = true, showQuickJumper = true
	} = data;
	// 计算总页数
	let totalPage = Math.ceil(total / pageSize);
	// 分页options
	if (!isUndefined(pageSizeOptions)) {
		root._data._pageSizeOptions = pageSizeOptions;
		// 默认被选择的pageSize
		if (isUndefined(root._data._pageSizeOptions.selected)) {
			root._data._pageSizeOptions.selected = 1;
		}
	}
	root._data = $.extend(root._data, {
		_totalPage: totalPage,
		_pageSize: pageSize,
		_pageNumber: pageNumber,
		_total: total,
		_showPageSizeChanger: showPageSizeChanger,
		_showQuickJumper: showQuickJumper
	});
	// 变化页码回调
	if (isFunction(onPageNumberChange)) {
		root._data._onPageNumberChange = onPageNumberChange;
	}
}

const setWrap = (root) => {
	let {
		_$dom, _showPageSizeChanger,
		_showQuickJumper, _onPageNumberChange, _pageSizeOptions
	} = root._data;
	// 控制pageSizeChanger显示
	if (!_showPageSizeChanger || isUndefined(_pageSizeOptions)) {
		_$dom.find(pageSizeChangerWrapClass).hide();
	}
	// 控制showQuickJumper显示
	if (!_showQuickJumper) {
		_$dom.find(quickJumperWrapClass).hide();
	}
}


// 类
class Pagination extends Base {
	constructor(data, pTarget) {
		super();
		// call jqIsReady
		jqIsReady();
		//保存dom数据
		appendDomValue(this, data);
		//保存其他数据
		appendOtherValue(this, data);
		// 保存_pTarget
		this._data._pTarget = isUndefined(pTarget) ? this : pTarget;
		// 每页显示的条数参数
		createPageSizeOption(this);
		//
		setWrap(this);
		// 初始化
		init(this);
	}

	set setParams(json) {
		let _data = this._data;
		let {_pageNumber, _pageSize, _total} = _data;
		let {pageNumber, pageSize, total} = json;
		if (isJson(json)) {
			if (isNumber(pageSize) && _pageSize !== pageSize) {
				// 一旦更改pageSize,默认将pageNumber设置为1
				this._data._pageNumber = 1;
				this._data._pageSize = pageSize;
				this._data._totalPage = Math.ceil(_data._total / _data._pageSize);
				// 该方法内会调用createNumBtn
				this.goPage(this._data._pageNumber);
			}
			if (isNumber(pageNumber) && _pageNumber !== pageNumber) {
				this._data._pageNumber = pageNumber;
				// 创建数字按钮
				createNumBtn(this);
			}
			if (isNumber(total) && _total !== total) {
				this._data._total = total;
				this._data._totalPage = Math.ceil(total / _data._pageSize);
				// 创建数字按钮
				createNumBtn(this);
			}
			// // 改变样式
			setStyle(this);
		} else {
			throw new TypeError(`分页data值类型错误, 参数必须是json类型,\n当前参数=${json}`);
		}
	}

	get pageSize() {
		return this._data._pageSize;
	}

	get pageNumber() {
		return this._data._pageNumber;
	}

	get total() {
		return this._data._total;
	}

	remove() {
		this._data._$wrapper.empty();
		this._data = null;
	}

	// 转到第几页
	goPage(num) {
		let {_pageSize, _onPageNumberChange, _totalPage, _$input, _pTarget} = this._data;
		let intNum = parseInt(num);
		if (isNumber(intNum) && intNum > 0) {
			this._data._pageNumber = intNum;
			if (num > 0 && num <= _totalPage) {
				// 用户定义了onChange
				if (isFunction(_onPageNumberChange)) {
					_onPageNumberChange.apply(_pTarget, [{pageNumber: intNum, pageSize: _pageSize}]);
				}
			} else if (num > _totalPage) {
				this.goPage(_totalPage);
			} else if (num < 0) {
				this.goPage(1);
			}
			_$input.val("");
			createNumBtn(this);
			setStyle(this);
		} else if (intNum < 0) {
			this.goPage(1);
		} else {
			throw new TypeError(`分页goPage()错误`);
		}
	}
}

// page	当前页数	number	-
// total	数据总数	number	0
// pageSize	每页条数	number	-
// defaultPageSize	默认的每页条数	number	10
// showPageSizeChanger	是否可以改变 pageSize 	boolean	false
// pageSizeOptions	指定每页可以显示多少条	string[]	['10', '20', '30', '40']
// showQuickJumper	是否可以快速跳转至某页	boolean	false
// onChange	页码改变的回调，参数是改变后的页码及每页条数	Function(page, pageSize)	noop

// 初始化
const init = (root) => {
	let {_$wrapper, _pageNumber, _total, _$dom} = root._data;
	if (isNumber(_pageNumber) && isNumber(_total)) {
		// 创建数字按钮
		createNumBtn(root);
	}
	// 绑定事件
	bindEvent(root);
	//设置pageSize
	createPageSizeOption(root);
	// 设置样式
	setStyle(root);
	// 清空容器
	_$wrapper.empty();
	// 添加dom
	_$dom.appendTo(_$wrapper);
}

// 修改分页尺寸
const setSize = (data) => {
	switch (data) {
		case "small":

			break;
		case "normal":

			break;
		case "large":

			break;
	}
}

const setStyle = (root) => {
	let {_$prev, _$next, _pageNumber, _totalPage} = root._data;
	// 第一页
	if (_pageNumber === 1) {
		_$prev.addClass(disableClass);
	} else {
		_$prev.removeClass(disableClass);
	}
	// 最后一页
	if (_pageNumber === _totalPage) {
		_$next.addClass(disableClass);
	} else {
		_$next.removeClass(disableClass);
	}
}

const bindEvent = (root) => {
	let _data = root._data;
	let {_$pageSizeNumWrap, _$options, _$optionsList, _$numWrap, _$prev, _$next, _$goBtn, _$input} = _data;
	//数字按s钮点击
	_$numWrap.on("click", numClass, function () {
		if (!$(this).hasClass(numSelectedClass)) {
			let page = parseInt($(this).text());
			if (isNumber(page)) {
				root.goPage(page);
			}
		}
	});
	_$prev.on("click", function () {
		let {_pageNumber} = _data;
		if (_pageNumber !== 1) {
			root.goPage(_pageNumber - 1);
		}
	});
	_$next.on("click", function () {
		let {_pageNumber, _totalPage} = _data;
		if (_pageNumber !== _totalPage) {
			root.goPage(_pageNumber + 1);
		}
	});
	// 转到x页
	_$goBtn.on("click", function () {
		if (_$input.val() !== "") {
			let page = parseInt(_$input.val());
			if (isNumber(page)) {
				root.goPage(page);
			}
		}
	});
	// input回车
	_$input.on("keyup", function (e) {
		if (e.keyCode === 13) {
			_$goBtn.trigger("click");
		}
	});
	// $options
	_$options.on("mouseenter", function (e) {
		_$optionsList.show();
	}).on("mouseleave", function (e) {
		_$optionsList.hide();
	});
	//$options
	_$options.on("click", opsItemClass, function () {
		let intNum = parseInt($(this).text());
		if (isNumber(intNum)) {
			$(this).addClass(itemSelectedClass).siblings().removeClass(itemSelectedClass);
			_$optionsList.hide();
			_$pageSizeNumWrap.text(intNum);
			root.setParams = {pageSize: intNum};
		}
	});
}


const createNumBtn = (root) => {
	let {_pageNumber, _pageSize, _total, _totalPage, _$numWrap} = root._data;
	// 容器缩小,可能导致_pageNumber>_totalPage
	if (_pageNumber > _totalPage) {
		_pageNumber = _totalPage;
	}
	let items = "";
	if (_totalPage > 6) {
		// 1, 2, 3, 4, 5, 6
		//  第一页 *************************************************
		if (_pageNumber !== 1) {
			items += `<li><a>1</a></li>`;
		}
		//  左侧... *************************************************
		if (_pageNumber >= 4) {
			// 左侧...
			items += `<li class="pagination-jump-prev"><a class="vicon vicon-morehor"></a></li>`;
		}
		// 中间数字-3 *************************************************
		if (_pageNumber === _totalPage && _pageNumber - 4 > 1) {
			items += `<li><a>${_pageNumber - 4}</a></li>`;
		}
		// 中间数字-3 *************************************************
		if (_pageNumber > _totalPage - 2 && _pageNumber - 3 > 1) {
			items += `<li><a>${_pageNumber - 3}</a></li>`;
		}
		// 中间数字-2 *************************************************
		if (_pageNumber > _totalPage - 3 && _pageNumber - 2 > 1) {
			items += `<li><a>${_pageNumber - 2}</a></li>`;
		}
		// 中间数字-1 *************************************************
		if (_pageNumber - 1 > 1) {
			items += `<li><a>${_pageNumber - 1}</a></li>`;
		}
		// 中间数字 *************************************************
		items += `<li class="${numSelectedClass}"><a>${_pageNumber}</a></li>`;
		// 中间数字+1 *************************************************
		if (_pageNumber + 1 < _totalPage) {
			items += `<li><a>${_pageNumber + 1}</a></li>`;
		}
		// 中间数字+2 *************************************************
		if (_pageNumber <= 3 && _pageNumber + 2 < _totalPage) {
			items += `<li><a>${_pageNumber + 2}</a></li>`;
		}
		// 中间数字+3 *************************************************
		if (_pageNumber <= 2 && _pageNumber + 3 < _totalPage) {
			items += `<li><a>${_pageNumber + 3}</a></li>`;
		}
		// 中间数字+4 *************************************************
		if (_pageNumber === 1 && _pageNumber + 4 < _totalPage) {
			items += `<li><a>${_pageNumber + 4}</a></li>`;
		}
		// 右侧... *************************************************
		if (_totalPage - _pageNumber > 2) {
			items += `<li class="pagination-jump-next"><a class="vicon vicon-morehor"></a></li>`;
		}
		// 最后一页 *************************************************
		if (_totalPage !== _pageNumber) {
			items += `<li><a>${_totalPage}</a></li>`;
		}
		// 生成html *************************************************
	} else {
		// 总页数<=5
		for (let num = 1; num <= _totalPage; num++) {
			let c = num === _pageNumber ? numSelectedClass : "";
			items += `<li class="${c}"><a>${num}</a></li>`;
		}
	}
	// 插入html
	_$numWrap.html(items);
}

// 修改pageSize
const createPageSizeOption = (root) => {
	let {_pageSizeOptions, _$pageSizeNumWrap, _$options} = root._data;
	if (isJson(_pageSizeOptions)) {
		let $itemWrap = _$options.find(optionsItemWrapClass);
		let dom = "", c, selected = _pageSizeOptions.selected;
		for (let i = 0; i < _pageSizeOptions.options.length; i++) {
			let item = _pageSizeOptions.options[i];
			if (i === selected - 1) {
				c = itemSelectedClass;
				_$pageSizeNumWrap.text(item);
				root._data._pageSize = Number(item);
			} else {
				c = "";
			}
			dom += `<li class="${c}">${item}</li>`;
		}
		$itemWrap.children().remove();
		$(dom).appendTo($itemWrap);
	}
}

export default Pagination;
