'use strict';

import Base from "./common/Base";
import Thead from "./table_v0.3/Thead";
import Tbody from "./table_v0.3/Tbody";
import Footer from "./table_v0.3/Footer";
import Layout from "./table_v0.3/Layout";
import validateInitData from "./table_v0.3/validateInitData";
import setEachWraps from "./table_v0.3/setEachWraps";
import scrollbarSize from "./table_v0.3/scrollbarSize";
import {isArray, isFunction, isJson, isString, once, isBoolean} from "./common/util";
import {trMaker} from "./table_v0.3/createTr";
import parseColumns from "./table_v0.3/parseColumns";
import tableEvent from "./table_v0.3/tableEvent";
import Colgroup from "./table_v0.3/Colgroup";
import tpl from "./common/tpl";
import html from "../../template/table.html";
import isNumber from "lodash/isNumber";
import setMainCheckboxTbody from "./table_v0.3/setMainCheckboxTbody";
import isUndefined from "lodash/isUndefined";
import scrollHandler from "./table_v0.3/scrollHandler";
import onResize from "./common/onResize";
// import createCols from "./table_v0.3/colsHandler";


const trClass = "v-table-tr",
	_trClass = `.${trClass}`,
	checkboxClass = "v-table-checkbox",
	propChecked = "checked",
	dataForRowData = "v-table-rowData",
	mainCheckboxClass = "v-table-main-checkbox"
;


//
const init = ({_root, initData}) => {
	// get scrollbar size
	let {scrollbarWidth, scrollbarHeight} = scrollbarSize(initData.el);
	// get ajax information
	let {ajax} = initData;
	// $el
	let $el = $(initData.el);
	$el.empty();
	// $wrap
	let $wrap = $(tpl(html));
	// set _data
	_root._data = {initData, scrollbarWidth, scrollbarHeight, $el, $wrap};
	let {dataSource} = initData;
	// parseColumns
	parseColumns(_root);
	// get wraps then store wraps
	setEachWraps(_root);
	// table event handler
	tableEvent(_root);
	// setCheckboxTbody
	setMainCheckboxTbody(_root);
	// create colgroup(必须在layout之前初始化)
	let colgroup = new Colgroup(_root);
	// create instance
	let layout = new Layout(_root);
	// create thead
	let thead = new Thead(_root);
	// create tbody
	let tbody = new Tbody(_root);
	// create footer
	let footer = new Footer(_root);
	//
	thead.init();
	// scrollHandler
	scrollHandler(_root);
	//
	$.extend(_root._data, {colgroup, layout, thead, tbody, footer});
	// render dataSource
	if (isJson(dataSource)) {
		_root.dataSource = dataSource;
	} else if (isJson(ajax) && isString(ajax.url)) {
		_root.ajax(ajax);
	}
	// append
	$wrap.appendTo($el);
	// bundleMethods
	_root.bundleMethods(_root._data.initData.methods, _root);
}

// 表格创建完之后执行
const tableMounted = _root => {
	// console.log("------------------- tableMounted() -------------------");
	let {mounted} = _root._data.initData;
	isFunction(mounted) && mounted(_root);
}

const getCheckedChangeData = ({_root, trIndex, $checkboxs}) => {
	let rowData = _root.rowData(trIndex),
		$leftTr = _root.$leftTr(trIndex),
		$rightTr = _root.$rightTr(trIndex),
		$centerTr = _root.$centerTr(trIndex),
		$checkbox = $checkboxs.eq(trIndex),
		isChecked = $checkbox.prop(propChecked),
		$td = $checkbox.closest("td"),
		isCheckAll = _root.isCheckAll,
		$tr = $centerTr;
	//
	$leftTr && ($tr = $tr.add($leftTr));
	$rightTr && ($tr = $tr.add($rightTr));
	//
	return {
		isCheckAll, isChecked, rowData, trIndex,
		$tr, $td, $leftTr, $rightTr, $centerTr, $checkbox
	}
}

// dataSource类型错误
const dataSourceTypeError = data => {
	throw new TypeError(`table组件错误;dataSource必须是json,并且dataSource.data必须是数组.\n当前dataSource=${JSON.stringify(data)}`);
}

// 设置默认缩放比例
const setDefaultScale = initData => {
	for (let column of initData.columns) {
		isUndefined(column.scale) && (column.scale = 1);
	}
}

class Table extends Base {
	constructor(initData) {
		super();
		let _root = this;
		// 检测初始化参数
		validateInitData(initData);
		let {el, pagination} = initData;
		// 设置默认scale
		setDefaultScale(initData);
		// 判断分页参数
		if (pagination) { // 容器有尺寸，并且可见的时候，才开始初始化
			// 如果固定行数，不显示更改pageSize模块
			isNumber(pagination.pageSize) && (pagination.showPageSizeChanger = false);
			//
			if (el.width() > 0 && el.height() > 0 && el.is(":visible")) {
				init({initData, _root});
			} else {
				onResize(el, (w, h) => {
					let visible = el.is(":visible");
					if (w > 0 && h > 0 && visible) {
						init({initData, _root});
					}
				});
			}
		} else {
			init({initData, _root});
		}
	}

	ajax(ops = {}) {
		let _root = this;
		// default url
		ops.url = ops.url || _root.ajaxUrl;
		//
		if (isJson(ops) && isString(ops.url)) {
			_root.showLoading();
			//
			let pageNumber,
				pageSize = _root._data.layout.pageSize;
			//默认ops.data
			!ops.data && (ops.data = {});
			// 设置pageNumber
			if (ops.data.pageNumber) { // 优先使用用户传的pageNumber
				pageNumber = ops.data.pageNumber;
			} else if (_root._data.paginationIns) { // 否则调用分页传回的pageNumber
				pageNumber = _root._data.paginationIns.pageNumber
			} else { // 默认pageNumber=1
				pageNumber = 1;
			}
			// 增加page相关参数
			!ops.data.pageNumber && (ops.data.pageNumber = pageNumber);
			ops.data.pageSize = pageSize;
			//
			_root.ajaxUrl = ops.url;
			_root.ajaxData = ops.data;
			//
			_root._data.ajaxOps = ops;
			// ajax
			$.ajax(ops).then(data => {
				_root.hideLoading();
				_root.dataSource = data;
				_root.setMainCheckbox();
			}, e => {
				let {ajaxError} = _root._data.initData;
				if (isFunction(ajaxError)) {
					ajaxError(e);
				}
			});
		} else {
			throw new TypeError(`表格ajax方法传入的参数错误,当前参数=${isJson(ops) ? JSON.stringify(ops) : ops}`);
		}
	}

	// set dataSource
	set dataSource(data) {
		// 如果没有data属性，有datas属性，那么按照datas渲染
		if (!data.data && data.datas && isArray(data.datas)) {
			data.data = data.datas;
		}
		// 如果返回数据是string,将尝试转成json
		// try {
		// 	isString(data) && (data = JSON.parse(data));
		// } catch (e) {
		// 	// 返回数据类型错误处理
		// 	dataSourceTypeError(data);
		// }
		// 处理json
		if (isJson(data) && isArray(data.data)) {
			this._data.dataSource = data;
			let {layout, thead, tbody, footer, initData: {pagination}, $wrap, paginationIns} = this._data;
			// 当请求所有数据的时候，pageSize=all
			data.pageSize === "all" && (layout.pageSize = data.length);
			// 判断分页
			if (pagination) { // 有分页
				// 分页不存在||total改变||pageNumber改变就初始化footer
				if (!paginationIns || paginationIns.total !== data.total || paginationIns.pageNumber !== data.pageNumber) {
					footer.init(this);
				}
			} else {// 无分页
				footer.noPagination();
			}
			//
			tbody.init();
			// set main checkbox
			this.setMainCheckbox();
			// remove visibility
			$wrap.css("visibility", "");
			// table created
			tableMounted(this);
			//
			layout.resizeHandler();
			// create once
			if (data.data.length > 0) { //有表格数据
				// ----- 排序相关处理 START -----
				let {sortOrder, sortorder, sortFile, sortfile} = data,
					order = sortOrder || sortorder,
					field = sortFile || sortfile;
				// 返回的数据有排序信息
				if (order && field) {
					thead.sortBy({order, field});
				}
				// ----- 排序相关处理 END -----
			} else { // 无表格数据
				thead.noDataHandler();
				layout.noDataHandler();
			}
		} else {
			// 返回数据类型错误处理
			dataSourceTypeError(data);
		}
	}

	refresh() {
		let {pageNumber} = this._data.ajaxOps.data;
		// 用户将当前有数据的行全都删除了，然后调用refresh()的时候，pageNumber必须改变
		if (this.rowData().length === 0) {
			pageNumber = (--pageNumber === 0) ? 1 : pageNumber;
			this._data.ajaxOps.data.pageNumber = pageNumber;
		}
		//
		this.ajax(this._data.ajaxOps);
	}

	layout() {
		let {colgroup, layout} = this._data;
		colgroup.resize();
		layout.resizeHandler();
	}

	remove() {
		let {$el, $wrap} = this._data;
		// $wrap.off().remove();
		$el.find("*").off().remove();
		super.remove();
	}

	showLoading(txt) {
		let {footer} = this._data;
		footer.showLoading(txt);
	}

	hideLoading(txt) {
		let {footer} = this._data;
		footer.hideLoading(txt);
	}

	$tr(i) {
		if (isNumber(i)) {
			return this.$allTr(i);
		} else {
			throw new TypeError("$tr()参数必须是数字");
		}
	}

	// return $tr
	$allTr(i) {
		let {leftColumns, rightColumns} = this._data,
			$returnVal = this.$centerTr(i);
		// left
		leftColumns && ($returnVal = $returnVal.add(this.$leftTr(i)));
		// right
		rightColumns && ($returnVal = $returnVal.add(this.$rightTr(i)));
		// return
		return $returnVal;
	}

	$centerTr(i) {
		let {_$cBTbody} = this._data,
			$returnVal = _$cBTbody.find(_trClass);
		if (isNumber(i)) {
			$returnVal = $returnVal.eq(i);
		}
		// return
		return $returnVal;
	}

	$leftTr(i) {
		let {_$lBTbody, leftColumns} = this._data,
			$returnVal = null;
		//
		if (leftColumns && _$lBTbody) {
			$returnVal = _$lBTbody.find(_trClass);
			//
			if (isNumber(i)) {
				$returnVal = $returnVal.eq(i);
			}
		}
		// return
		return $returnVal;
	}

	$rightTr(i) {
		let {_$rBTbody, rightColumns} = this._data,
			$returnVal = null;
		//
		if (rightColumns && _$rBTbody) {
			$returnVal = _$rBTbody.find(_trClass);
			//
			if (isNumber(i)) {
				$returnVal = $returnVal.eq(i);
			}
		}
		// return
		return $returnVal;
	}

	isChecked(trIndex) {
		let {tableCheckboxEnable} = this._data;
		if (tableCheckboxEnable) {
			// trIndex
			if (isNumber(trIndex)) {
				let returnVal = false;
				let {$checkboxTbody} = this._data;
				returnVal = $checkboxTbody.find(_trClass).eq(trIndex).find(`.${checkboxClass}`).prop(propChecked);
				// return
				return returnVal;
			} else {
				throw new Error(`table.isChecked()参数类型错误,当前参数=${trIndex}`);
			}
		} else {
			return false;
		}
	}

	checkRow(ops) {
		this.checkRows(ops);
	}

	// 选择行(isCheckAll, isChecked, rowData, $checkbox, $tr, $td, $centerTr, $leftTr, $rightTr, trIndex)
	checkRows(ops) {
		let {tableCheckboxEnable} = this._data;
		if (tableCheckboxEnable) {
			if (isFunction(ops) || isArray(ops) || isNumber(ops) || isBoolean(ops)) {
				let _root = this, {initData: {onCheckedChange}, $checkboxTbody} = _root._data,
					$checkboxs = $checkboxTbody.find(`.${checkboxClass}`),
					rowData = _root.rowData(), checkedChangeData;
				//
				if (isFunction(ops)) {
					for (let trIndex = 0; trIndex < rowData.length; trIndex++) {
						let currentIsChecked = _root.isChecked(trIndex);
						//
						checkedChangeData = getCheckedChangeData({_root, trIndex, $checkboxs});
						// get return val
						let returnVal = ops(checkedChangeData, _root);
						// change state
						if (currentIsChecked !== returnVal) {
							//
							let $checkbox = checkedChangeData.$checkbox;
							// set checkbox by return val
							$checkbox.prop(propChecked, returnVal);
							//set isChecked by return value
							checkedChangeData.isChecked = returnVal;
							// call onCheckedChange
							isFunction(onCheckedChange) && onCheckedChange(checkedChangeData, _root);
						}
					}
				} else if (isArray(ops)) {
					ops.forEach(trIndex => {
						// current
						let currentIsChecked = _root.isChecked(trIndex);
						//
						if (currentIsChecked !== true) {
							$checkboxs.eq(trIndex).prop(propChecked, true);
							//
							checkedChangeData = getCheckedChangeData({_root, trIndex, $checkboxs});
							// call onCheckedChange
							isFunction(onCheckedChange) && onCheckedChange(checkedChangeData, _root);
						}
					})
				} else if (isNumber(ops)) {
					// current
					let currentIsChecked = _root.isChecked(ops);
					//
					if (currentIsChecked !== true) {
						$checkboxs.eq(ops).prop(propChecked, true);
						//
						checkedChangeData = getCheckedChangeData({_root, trIndex: ops, $checkboxs});
						// call onCheckedChange
						isFunction(onCheckedChange) && onCheckedChange(checkedChangeData, _root);
					}
				} else if (isBoolean(ops)) {
					$checkboxs.each((trIndex) => {
						// current
						let currentIsChecked = _root.isChecked(trIndex);
						// call onCheckedChange when change state
						if (currentIsChecked !== ops) {
							//
							checkedChangeData = getCheckedChangeData({_root, trIndex, $checkboxs});
							//
							let {$checkbox} = checkedChangeData;
							//set checkbox by ops
							$checkbox.prop(propChecked, ops);
							//
							checkedChangeData.isCheckAll = _root.isCheckAll;
							checkedChangeData.isChecked = ops;
							// call onCheckedChange
							isFunction(onCheckedChange) && onCheckedChange(checkedChangeData, _root);
						}
					})
				}
				// setMainCheckbox
				this.setMainCheckbox();
			} else {
				throw new Error("table.checkRows()参数类型错误");
			}
		} else {
			throw new Error("无法调用table.checkRows(),table.checkbox未定义");
		}
	}

	// 不选择某行
	uncheckRows(ops) {
		let {tableCheckboxEnable} = this._data;
		if (tableCheckboxEnable) {
			if (isArray(ops) || isNumber(ops) || isBoolean(ops)) {
				let _root = this, {initData: {onCheckedChange}, $checkboxTbody} = _root._data,
					$checkboxs = $checkboxTbody.find(`.${checkboxClass}`),
					checkedChangeData;
				//
				if (isArray(ops)) {
					ops.forEach(trIndex => {
						// current
						let currentIsChecked = _root.isChecked(trIndex);
						//
						if (currentIsChecked !== false) {
							$checkboxs.eq(trIndex).prop(propChecked, false);
							//
							checkedChangeData = getCheckedChangeData({_root, trIndex, $checkboxs});
							// call onCheckedChange
							isFunction(onCheckedChange) && onCheckedChange(checkedChangeData, _root);
						}
					})
				} else if (isNumber(ops)) {
					// current
					let currentIsChecked = _root.isChecked(ops);
					//
					if (currentIsChecked !== false) {
						$checkboxs.eq(ops).prop(propChecked, false);
						//
						checkedChangeData = getCheckedChangeData({_root, trIndex: ops, $checkboxs});
						// call onCheckedChange
						isFunction(onCheckedChange) && onCheckedChange(checkedChangeData, _root);
					}
				} else if (isBoolean(ops)) {
					$checkboxs.each((trIndex) => {
						// current
						let currentIsChecked = _root.isChecked(trIndex);
						// change state
						if (currentIsChecked !== !ops) {
							//
							checkedChangeData = getCheckedChangeData({_root, trIndex, $checkboxs});
							//
							let {$checkbox} = checkedChangeData;
							//set checkbox by ops
							$checkbox.prop(propChecked, !ops);
							//
							checkedChangeData.isCheckAll = _root.isCheckAll;
							checkedChangeData.isChecked = ops;
							// call onCheckedChange
							isFunction(onCheckedChange) && onCheckedChange(checkedChangeData, _root);
						}
					})
				}
				// setMainCheckbox
				this.setMainCheckbox();
			} else {
				throw new Error("table.uncheckRows()参数类型错误");
			}
		} else {
			throw new Error("无法调用table.uncheckRows(),table.checkbox未定义");
		}
	}

	// 返回行数据
	rowData(ops) {
		let returnVal = [],
			{_$cBTbody} = this._data,
			$trs = _$cBTbody.find(_trClass);
		//
		if (isUndefined(ops)) { //默认返回所有的rowdata
			$trs.each((i, tr) => {
				let json = $(tr).data(dataForRowData);
				isJson(json) && returnVal.push(json);
			})
		} else {
			if (isNumber(ops)) {
				let json = $trs.eq(ops).data(dataForRowData);
				isJson(json) && (returnVal = json);
			} else if (isArray(ops)) {
				for (let i of ops) {
					let json = $trs.eq(i).data(dataForRowData);
					isJson(json) && returnVal.push(json);
				}
			}
		}
		return returnVal;
	}

	// 添加行
	addRow(rowData) {
		let _root = this;
		let {layout} = this._data;
		try {
			if (isJson(rowData)) {
				let trIndex = _root.dataSource.data.length;
				_root.dataSource.data.push(rowData)
				// tr maker
				trMaker(_root, rowData, trIndex);
				// size handler
				layout.resizeHandler();
				// setCheckboxTbody
				_root.setMainCheckbox(_root);
			}
		} catch (e) {
			console.error(`table.addRow(rowData)失败，rowData必须是json`);
			throw new Error(e);
		}
	}

	// set main checkbox
	setMainCheckbox() {
		// 判断是否有table checkobx
		let {tableCheckboxEnable} = this._data;
		//
		if (tableCheckboxEnable) {
			let {isCheckAll, $mainCheckbox, checkedRowData} = this;
			let indeterminateClass = "indeterminate",
				$span = $mainCheckbox.closest("span.v-check");
			//
			$mainCheckbox.prop(propChecked, isCheckAll);
			//
			if (!isCheckAll && checkedRowData.length > 0) { // 部分选择
				$span.addClass(indeterminateClass);
			} else {
				$span.removeClass(indeterminateClass);
			}
			//
			return {isCheckAll};
		}
	}

	get dataSource() {
		return this._data.dataSource;
	}

	// set ajax url
	set ajaxUrl(url) {
		this._data.ajaxUrl = url;
	}

	// get ajax url
	get ajaxUrl() {
		return this._data.ajaxUrl;
	}

	// get ajax data
	set ajaxData(newData) {
		this._data.ajaxData = newData;
	}

	// get ajax data
	get ajaxData() {
		return this._data.ajaxData;
	}

	get $dom() {
		return this._data._$wrapper;
	}

	get pageNumber() {
		let paginationIns = this._data.paginationIns;
		if (paginationIns) {
			return paginationIns.pageNumber;
		} else {
			console.error("不存在分页，无法返回pageNumber");
			return 0;
		}
	}

	get pageSize() {
		let {layout} = this._data;
		return layout.pageSize;
	}

	// 返回checked的行数据
	get checkedRowData() {
		let {_$cBTbody, $checkboxTbody} = this._data,
			$trs = _$cBTbody.find(_trClass),
			$checkboxTrs = $checkboxTbody.find(_trClass),
			returnArr = [];
		//
		$checkboxTrs.each(i => {
			let isChecked = $checkboxTrs.eq(i).find(`.${checkboxClass}`).prop(propChecked);
			//
			isChecked && returnArr.push($trs.eq(i).data(dataForRowData));
		});
		//
		return returnArr;
	}

	// get main checkbox
	get $mainCheckbox() {
		let {$wrap} = this._data;
		return $wrap.find(`.${mainCheckboxClass}`);
	}

	get isCheckAll() {
		let {checkedRowData, dataSource: {data}, $mainCheckbox, _data: {_$cBTbody}} = this,
			isCheckAll = false,
			trLength = _$cBTbody.find("tr").length,
			dataLength = data.length,
			checkRowsLength = checkedRowData.length;
		//
		if (checkRowsLength === dataLength || checkRowsLength === trLength) { //全选
			$mainCheckbox.prop(propChecked, true);
			isCheckAll = true;
		}
		return isCheckAll;
	}

	get $checkedTr() {
		let returnVal = this.$tr,
			$leftTr = this.$leftTr(),
			$rightTr = this.$leftTr();
		//
		$leftTr && (returnVal = returnVal.add($leftTr));
		$rightTr && (returnVal = returnVal.add($rightTr));
		//
		return returnVal;
	}
}

export default Table;
