'use strict';

import {isJson, isArray, isString, isJq, isFunction} from "./common/util";
import {storage} from "./common/storage";

const formPostfix = "_FormData",
	checkedReg = /^(?:radio|checkbox)$/i,
	selectReg = /^(?:option|select-one|select-multiple)$/i,
	valueReg = /^(?:button|color|date|datetime|datetime-local|email|hidden|month|number|password|range|reset|search|submit|tel|text|textarea|time|url|week)$/i;


const setValue = ({$target, value}) => {
	let {type, property} = getInformation($target);
	if (valueReg.test(type)) {
		let cv = value ? value : "";
		$target.val(cv);
	} else if (selectReg.test(type)) {
		$target.find("option").each((i, dom) => {
			let checked = false;
			if (value) {
				let valueArr = isArray(value) ? value : value.split(",");
				let currVal = $(dom).text();
				checked = $.inArray(currVal, valueArr) !== -1;
			}
			$(dom).prop(property, checked);
		});
	} else if (checkedReg.test(type)) {
		$target.each((i, dom) => {
			let checked = false;
			if (value) {
				let valueArr = isArray(value) ? value : value.split(",");
				let currVal = $(dom).attr("value");
				checked = $.inArray(currVal, valueArr) !== -1;
			}
			$(dom).prop(property, checked);
		});
	} else {
		console.warn(`表单反序列化出错(序列化表单错误)，请联系qq:12687185`);
	}
}

const getInformation = ($target) => {
	let property;
	// set type
	let type = $target[0].type || $target[0].nodeName;
	// set property
	if (checkedReg.test(type)) {
		property = "checked"
	} else if (selectReg.test(type)) {
		property = "selected"
	} else if (valueReg.test(type)) {
		property = "value"
	}
	// return
	return {type, property};
}

const validateForm = (form, fun) => {
	if (isJq(form)) {
		return true;
	} else {
		throw new Error(`v.form.${fun}({form:xxx})参数类型错误,参数form必须是jquery对象`);
	}
}
const validateAlias = (alias, fun) => {
	if (isString(alias)) {
		return true;
	} else {
		throw new Error(`v.form.${fun}({alias:xxx})参数类型错误,参数alias必须是String`);
	}
}

const form = {
	store({form, alias, realtime = false, success, error}) {
		/**
		 * 保存表单序列化对象
		 * @param form 表单jquery DOM对象
		 * @returns {} 表单序列化对象
		 */
		if (validateForm(form, "store") && validateAlias(alias, "store")) {
			try {
				let serializeData = this.serialize({form});
				storage.setItem(alias + formPostfix, serializeData).then(() => {
					// console.log("data saved");
				});
				// 实时保存表单数据
				if (realtime) {
					form.find("[name]").on("input change", (e) => {
						this.store({form, alias, realtime, success, error});
					})
				}
				if (isFunction(success)) {
					success(serializeData);
				}
				return serializeData;
			} catch (e) {
				if (isFunction(error)) {
					error({form, alias, realtime});
				}
			}
		}
	},
	serialize({form}) {
		/**
		 * 序列化表单元素为JSON对象
		 * @param Form 表单jquery DOM对象
		 * @returns {}
		 */
		if (validateForm(form, "serialize")) {
			let dataArray = form.serializeArray();
			let result = {};
			dataArray.forEach(({name, value}) => {
				if (!result[name]) {
					//获取当前表单控件元素
					let element = form.find("[name='" + name + "']")[0];
					//获取当前控件类型
					let type = element.type || element.nodeName;
					//如果控件类型为多选那么值就是数组形式，否则就是单值形式。
					result[name] = (/^(select-multiple|checkbox)$/i).test(type) ? [value] : value;
				} else {
					//保存多个值
					result[name].push(value);
				}
			});
			return result;
		}
	},
	deserialize({form, alias, success, error}) {
		if (validateForm(form, "deserialize") && validateAlias(alias, "deserialize"))
			try {
				storage.getItem(alias + formPostfix).then((formData) => {
					if (isJson(formData)) {
						form.find("[name]").each((i, dom) => {
							let $target = $(dom);
							let name = $target.attr("name");
							let value = formData[name];
							setValue({$target, value});
						});
						if (isFunction(success)) {
							success(formData);
						}
					} else {
						success(null);
					}
				});
			} catch (e) {
				if (error && isFunction(error)) {
					error({form, alias});
				}
				throw new Error(`v.form.deserialize()失败`);
			}
	},
	remove(arg) {
		try {
			if (isJson(arg)) {
				let {alias} = arg;
				arg = alias;
			}
			if (isString(arg)) {
				return storage.removeItem(arg + formPostfix);
			}
		} catch (e) {
			throw new Error(`v.form.remove(form)失败`);
		}
	}
}

export default form;
