'use strict';

import {isJson} from "../common/util";

// set real module
const rModule = {
	set moduleProxy(proxyJson) {
		this._moduleProxy = proxyJson;
	},
	get moduleProxy() {
		return this._moduleProxy;
	},
	getRealModule(module) {
		let moduleProxy=this.moduleProxy,
			realModule = moduleProxy,
			moduleArr = module.split("/");
		//
		if (isJson(this.moduleProxy)) {
			try {
				for (let val of moduleArr) {
					if (realModule[val]) {
						realModule = realModule[val];
					} else {
						realModule = null;
						break;
					}
				}
				if (isJson(realModule)) {
					realModule = realModule.on;
				}
			} catch (e) {
			}
		}
		if (realModule) {
			module = realModule;
		}
		return module;
	}
}

export default rModule;