'use strict';
import {isJson, isFunction, isString, isJq} from "./common/util";
import loadBodyHtml from "./common/loadBodyHtml";
import router from "./router/router";
import globalData from "./common/globalData";
import rModule from "./router/realModule";

const submodule = ({module, params, el}) => {
	let requireJs = globalData.requireJs;
	if (isFunction(requireJs)) {
		// main deferred
		let mainDef = $.Deferred(),
			realModule = rModule.getRealModule(module);
		let context = {};
		const mainDefResolve = (obj) => {
			mainDef.resolve(obj);
		}
		let $domSetter = {};
		Object.defineProperties($domSetter, {
			$dom: {
				set($val) {
					// 优先匹配el值
					if (el && isJq(el)) {
						el.children().remove();
						el.empty();
						$val.appendTo(el);
					} else if (params && isJq(params.$holder)) {
						// 没有el,但是有$holder参数
						params.$holder.children().remove();
						params.$holder.empty();
						$val.appendTo(params.$holder);
					}
					context.$dom = $val;
					mainDefResolve(context);
				}
			}
		})
		// 子模块返回的数据
		requireJs([realModule], (amdModule) => {
			//
			let {init, page} = amdModule;
			// copy module return value
			for (let p in amdModule) {
				context[p] = amdModule[p];
			}
			// call new realModule
			let execute = ($dom) => {
				if (isFunction(init)) {
					let submoduleParams = $.extend(true, {isSubmodule: true}, params, router.params);
					// execute realModule init
					init.apply($domSetter, [submoduleParams, $dom]);
				}
			};
			if (isJson(page)) {
				loadBodyHtml(page).then($bodyChildrenHtml => {
					execute($bodyChildrenHtml);
				})
			} else {
				execute();
			}
		})
		return mainDef.promise();
	}
}

export default submodule;

