'use strict';

const scrollEvent = "scroll.v-table",
	scrollEvent2 = "scroll.v-table",
	enterEvent = "mouseenter.v-table";

const scrollHandler = _root => {
	let {_$lBWrap, _$cBWrap, _$rBWrap, _$cHWrap, _$cHScrollWrap} = _root._data,
		$lcb = _$cBWrap;
	//
	_$lBWrap && ($lcb = $lcb.add(_$lBWrap));
	_$rBWrap && ($lcb = $lcb.add(_$rBWrap));
	//
	$lcb.each((i, tar) => {
		let $target = $(tar);
		$target.on(enterEvent, () => {
			// 先接触其他绑定
			$lcb.off(scrollEvent);
			// 绑定滚轮事件
			$target.on(scrollEvent, () => {
				let st = $target.scrollTop();
				$lcb.each((i, tar) => {
					tar !== $target[0] && $(tar).scrollTop(st);
				});
				//scrollLeft
				_$cHScrollWrap.scrollLeft(_$cBWrap.scrollLeft());
			})
		});
		// 用户直接控制滚轮处理
		$target.one(scrollEvent2, () => {
			$target.trigger(enterEvent);
		})
	})
}

export default scrollHandler;
