'use strict';

import {isJson} from "../common/util";
import createTd from "./createTd";

const hiddenClass = "v-table-hidden-t",
	trClass = "v-table-tr",
	_dataTrClass = "v-table-data-tr",
	expandTrClass = ".v-table-expanded-tr",
	$lastDataTrClass = `.${_dataTrClass}:last`;

// get last dataTr
const getLastDataTr = $tbody => {
	let $lastDataTr = $tbody.find($lastDataTrClass),
		$nextTr = $lastDataTr.next();
	//
	if ($nextTr.is(expandTrClass)) {
		$lastDataTr = $nextTr;
	}
	//
	return $lastDataTr;
}

// tr maker
const trMaker = (_root, rowData, trIndex) => {
	let {
			leftColumns, bodyColumns, rightColumns,
			_$lBTbody, _$cBTbody, _$rBTbody
		} = _root._data,
		dataTrClass = isJson(rowData) ? ` ${_dataTrClass}` : "",
		$leftTr, $centerTr, $rightTr, $nextTr, $lastDataTr,
		tr = `<tr class="${trClass}${dataTrClass}"></tr>`;
	//
	$lastDataTr = getLastDataTr(_$cBTbody);
	// $centerTr
	if ($lastDataTr.length === 1) {
		$centerTr = $(tr).insertAfter($lastDataTr);
	} else {
		$centerTr = $(tr).appendTo(_$cBTbody);
	}
	//
	isJson(rowData) && $centerTr.data("v-table-rowData", rowData);
	// $leftTr
	if (leftColumns) {
		$lastDataTr = getLastDataTr(_$lBTbody);
		if ($lastDataTr.length === 1) {
			$leftTr = $(tr).insertAfter($lastDataTr);
		} else {
			$leftTr = $(tr).appendTo(_$lBTbody);
		}
	}
	// $rightTr
	if (rightColumns) {
		$lastDataTr = getLastDataTr(_$rBTbody);
		if ($lastDataTr.length === 1) {
			$rightTr = $(tr).insertAfter($lastDataTr);
		} else {
			$rightTr = $(tr).appendTo(_$rBTbody);
		}
	}
	// create td
	for (let bodyColumn of bodyColumns) {
		// empty td
		let $td = $("<td></td>");
		// 有行数据
		if (isJson(rowData) && trIndex < _root.dataSource.data.length) {
			$td = createTd({_root, trIndex, $leftTr, $centerTr, $rightTr, rowData, bodyColumn});
		}
		//
		let tdHtml = $td.clone().addClass(hiddenClass).prop("outerHTML");
		//
		switch (bodyColumn.fixed) {
			case "left":
				leftColumns && $leftTr.append($td);
				$centerTr.append($(tdHtml));
				rightColumns && $rightTr.append($(tdHtml));
				break;
			case "right":
				leftColumns && $leftTr.append($(tdHtml));
				$centerTr.append($(tdHtml));
				rightColumns && $rightTr.append($td);
				break;
			default:
				leftColumns && $leftTr.append($(tdHtml));
				$centerTr.append($td);
				rightColumns && $rightTr.append($(tdHtml));
				break;
		}
	}
}
// createTr
const createTr = _root => {
	let {
			pageSize,
			_data: {
				dataSource: {data}, _$noDataWrap, layout
			}
		} = _root,
		dataLength = data.length;
	// 判断是否是全部行
	pageSize = pageSize === "all" ? data.length : pageSize;
	// 渲染数据 create tr
	for (let trIndex = 0; trIndex < pageSize; trIndex++) {
		let rowData = data[trIndex];
		trMaker(_root, rowData, trIndex);
	}
	// 判断是否有表格数据
	if (dataLength > 0) {
		// 有表格数据
		_$noDataWrap.hide();
	} else {
		// 无数据处理
		_$noDataWrap.show();
	}
}

export {createTr, trMaker};
