'use strict';

import {textTooltip, setBackround} from "./tooltip/tooltipSvg";
import {isString, isJson, isJq} from "./common/util";
import globalData from "./common/globalData";
import animationEnd from "./animationEnd";


export const tooltip = (ops, $outtarget, direction) => {
	if (isString(ops)) {
		return textTooltip(ops, $outtarget, direction);
	} else if (isJson(ops)) { // ops=$target, radius, triangleHeight, background
		if (isJq(ops.$target)) {
			// append dom
			ops.$target.closest("body").length !== 1 && ops.$target.appendTo("body");
			// setBackround
			return setBackround(ops);
		} else {
			throw new Error("v.tooltip()参数错误,$target必须是jquery对象");
		}
	}
}
// tooltip init
const mouseoverTooltip = (selector = "data-v-tooltip") => {
	// data-v-tooltip控制显示
	globalData.$body.on("mouseover", `[${selector}]`, e => {
		let $this = $(e.currentTarget);
		let text = $this.attr(selector);
		let direction = 'top';
		//前2个字符前缀可以指定气泡弹出的方向
		let tooldirection = text.substring(0, 2);
		let newtext = text.substring(3);
		switch (tooldirection) {
			case 'tc'://提示方向默认向上
				direction = 'top';
				break;
			case 'tl':
				direction = 'top-left';
				break;
			case 'tr':
				direction = 'top-right';
				break;
			case 'bc':
				direction = 'bottom';
				break;
			case 'bl':
				direction = 'bottom-left';
				break;
			case 'br':
				direction = 'bottom-right';
				break;
			case 'lc':
				direction = 'left';
				break;
			case 'lt':
				direction = 'left-top';
				break;
			case 'lb':
				direction = 'left-bottom';
				break;
			case 'rc':
				direction = 'right';
				break;
			case 'rt':
				direction = 'right-top';
				break;
			case 'rb':
				direction = 'right-bottom';
				break;
			default:
				direction = 'top';
				newtext = text;
		}

		let $tooltip = tooltip(newtext, $this, direction);
		$this.data("v-tooltip-target", $tooltip);
		//$tooltip && setPosition($this, $tooltip , direction);
	}).on("mouseout", `[${selector}]`, e => {
		/*$(e.currentTarget).data("v-tooltip-target").remove();*/
		let $removeTooltip = $(e.currentTarget).data("v-tooltip-target");
		$removeTooltip.removeClass('vZoomInSmall');
		$removeTooltip.addClass('vZoomOutSmall');
		animationEnd([$removeTooltip], function () {
			$removeTooltip.remove();
		});
	});
}

mouseoverTooltip();


