'use strict';

import {isJson, isString, isJq, isFunction, getLength, isEqual} from "../common/util";
import {queryStringToJson, jsonToQueryString} from "./paramsParser"
import loadBodyHtml from "../common/loadBodyHtml";
import globalData from "../common/globalData";
import {storage} from "../common/storage.js";
import vHistory from "./vHistory";
import rModule from "./realModule";


// call global before
const globalBefore = async (_root) => {
	if (_root._data && _root._data.customData && _root._data.customData.before) {
		let {before} = _root._data.customData,
			{module, moduleArr, params, realModule} = _root;
		if (isFunction(before)) {
			return await new Promise((resolve, reject) => {
				const beforeParams = {
					next: () => {
						resolve("next");
					},
					module, moduleArr, realModule, params
				}
				let returnValue = before(beforeParams);
				returnValue !== false && resolve();
			})
		}
	}
}

// call global after
const globalAfter = async ({_root}) => {
	let {_curr} = _root._data;
	if (_curr && _root._data && _root._data.customData && _root._data.customData.after) {
		let {after} = _root._data.customData,
			{module, params, realModule} = _root;
		if (isFunction(after)) {
			return await new Promise((resolve, reject) => {
				const afterParams = {
					next: () => {
						resolve("next");
					},
					module, realModule, params
				}
				let returnValue = after(afterParams);
				returnValue !== false && resolve();
			})
		}
	}
}

// call module after
const currModuleAfter = async (_curr) => {
	if (_curr && _curr._data && _curr._data.after) {
		let {after} = _curr._data;
		if (isFunction(after)) {
			return await new Promise((resolve, reject) => {
				let returnValue = after({next: resolve});
				returnValue !== false && resolve();
			})
		}
	}
}

// js not found handler
const notFoundHandler = ({_root, e}) => {
	if (_root._data && _root._data.customData) {
		console.error(`加载 '${e.requireModules}' 失败`);
		let {notFound} = _root._data.customData;
		if (isFunction(notFound)) {
			let {module, moduleArr, params, realModule} = _root;
			const notFoundParams = {
				module, moduleArr, realModule, params
			}
			notFound(notFoundParams);
		} else {

		}
	}
}

// enter module
const enterModuleHandler = async (newRoutingNode) => {
	// storage.set("v_router_lastPageSymbol", pageSymbol);
	// require new module
	await new Promise((resolve, reject) => {
		let realModule = newRoutingNode._data._root.realModule;
		globalData.requireJs([realModule], newModule => {
			let {_root, module, params, hash, pageSymbol, fromSymbol} = newRoutingNode._data;
			let {page, init, after} = newModule;
			// store after
			isFunction(after) && (newRoutingNode._data.after = after);
			// context
			let context = {module, realModule};
			// context
			Object.defineProperties(context, {
				$dom: {
					set(val) {
						if (isJq(val)) {
							if (_root.module === this.module) {
								this._$dom = val;
								if (isJq(this._$mainHolder)) {
									this._$dom.appendTo(this._$mainHolder);
								}
							}
						} else {
							throw new Error(`$dom值必须是jquery对象`);
						}
					}
				},
				$mainHolder: {
					set(val) {
						if (isJq(val)) {
							if (_root.module === this.module) {
								this._$mainHolder = val;
								this._$mainHolder.children().off().remove();
								this._$mainHolder.empty();
								if (isJq(this._$dom)) {
									this._$dom.appendTo(this._$mainHolder);
								}
							}
						} else {
							throw new Error(`$mainHolder值必须是jquery对象`);
						}
					}
				}
			})
			// 有页面需要加载
			if (page) {
				newRoutingNode._data.page = page;
				loadBodyHtml(page).then($bodyChildrenHtml => {
					isFunction(init) && init.apply(context, [params, $bodyChildrenHtml]);
					resolve();
				})
			} else {
				// 没有页面参数
				isFunction(init) && init.apply(context, [params]);
				resolve();
			}
		}, (e) => {
			let {_root} = newRoutingNode._data;
			notFoundHandler({_root, e});
			resolve();
		})
	})
}

class RoutingNode {
	constructor({_root, module, params}) {
		let realModule = rModule.getRealModule(module);
		this._data = {_root, module, realModule, params};
	}

	/*
	进入新模块
	需要设置_root.module,_root.params
	优先isFirstPage.
	处理_root._data._old.after相关方法
	保存_root._data._curr;
	triggerEvent: refresh|fistPage|goto|defaultRouter
	* */
	async enter({viewType, setState = "push", triggerEvent}) {
		let _this = this, hash,
			pageSymbol, fromSymbol,
			{_root, module, realModule, params} = _this._data,
			{_curr, customData} = _root._data,
			{hiddenRouter} = customData;
		// set page symbol
		// if (vHistory && vHistory.state && vHistory.state.pageSymbol) {
		// 	pageSymbol = Symbol();
		// }
		// // set from symbol
		// if (vHistory && vHistory.state && vHistory.state.pageSymbol) {
		// 	fromSymbol = vHistory.state.pageSymbol;
		// } else {
		// 	fromSymbol = viewType === "firstPage" ? "firstPage" : "null";
		// }
		// // set viewType
		// if (viewType === "auto") {
		//
		// }
		//await
		await currModuleAfter(_curr);
		await globalAfter({_root});
		// exchange curr&&old
		_root._data._curr = _this;
		// set _root attributes
		_root.module = module;
		_root.params = params;
		// save real module
		_root.realModule = realModule;
		// set state
		if (setState !== false) {
			// set hash
			let hashParams = isEqual(params, {}) ? "" : "/" + jsonToQueryString(params, true);
			if (triggerEvent !== "popstate") {
				// set Hash
				hash = hiddenRouter === true ? "" : "#" + module + hashParams;
				let historyCommand = "pushState";
				if (setState === "replace" || triggerEvent === "refresh") {
					historyCommand = "replaceState";
				}
				vHistory[historyCommand]({module, realModule, params, hash, pageSymbol, fromSymbol, setState});
			}
		}
		// globalBefore
		await globalBefore(_root);
		// enterModuleHandler
		await enterModuleHandler(_this);
	}

	// 设置参数, 暂时不允许replaceParams
	setParams(newParams, replaceParams = false) {
		if (!isJson(newParams)) {
			throw new Error(`路由参数必须是json! 当前参数${newParams}非法`)
		}
		let _this = this;
		let {_root, module, params} = _this._data;
		let paramsChanged = false;
		for (let p in newParams) {
			if (!params) {
				_this._data.params = newParams;
			} else if (newParams.hasOwnProperty(p) && !isEqual(newParams[p], params[p])) {
				params[p] = newParams[p];
				paramsChanged = true;
			}
		}
		// 确实修改参数值了
		if (paramsChanged) {
			_root.params = _this.params;
			// state
			let state = vHistory.state;
			// new params
			state.params = _this.params;
			// new hash
			state.hash = "#" + module.replace(/\/$/, "") + "/" + jsonToQueryString(state.params, true);
			// push state
			vHistory.pushState(state);
		}
	}

	get module() {
		return this._data.module;
	}

	set params(val) {
		this._data.params = val;
	}

	get params() {
		return this._data.params
	}

	// get hash() {
	// 	return "#" + this.module + "/" + jsonToQueryString(this.params);
	// }

	get viewType() {
		let {_root, module, params, hash, pageSymbol, fromSymbol} = this._data,
			{_old, _curr} = _root._data,
			_viewType, lastPageSymbol;
		lastPageSymbol = _curr && _curr._data ? _curr._data.fromSymbol : storage.get("v_router_lastPageSymbol");
		//
		if (_curr) {
			if (pageSymbol === lastPageSymbol)
				_viewType = "backward";
			else
				_viewType = "forward";
		} else {
			_viewType = "firstPage";
		}
		return _viewType;
	}
}

export default RoutingNode;
