'use strict';

export const scrollbar = {
	get width() {
		if (!this._width) {
			let scrollbarSize = scrollbarSize();
			this._width = scrollbarSize.width;
			this._height = scrollbarSize.height;
		}
		return this._width;
	},
	get height() {
		if (!this._height) {
			let scrollbarSize = scrollbarSize();
			this._width = scrollbarSize.width;
			this._height = scrollbarSize.height;
		}
		return this._height;
	}
}

export const scrollbarSize = () => {
	let oP = document.createElement('p'),
		styles = {
			width: '100px',
			height: '100px',
			overflowY: 'scroll',
			overflowX: 'scroll',
		}, i, width, height;
	for (i in styles) oP.style[i] = styles[i];
	document.body.appendChild(oP);
	width = oP.offsetWidth - oP.clientWidth;
	height = oP.offsetHeight - oP.clientHeight;
	oP.remove();
	return {width, height};
}