'use strict';
import {isString, isJq, isNumber, isJson} from "../common/util"
import tpl from "../common/tpl";
import tooltipTemplate from '../../../template/tooltipSvg.html';
import _vName from "../componentName";

const contentClass = "." + _vName + "-tooltip-content";
const rgbaReg = /^[rR][gG][bB][aA]\((\d{0,3},){3}0?\.?\d+\)$/;
const defaultTriangleH = 4;//箭头大小

// outerWidth, outerHeight, radius, triangleHeight, background
function createRoundRectSvg({outerWidth, outerHeight, radius = 4, background = 'rgba(20,20,20,.9)', triangleHeight = defaultTriangleH, stroke, strokeWidth = 1,new_dir}) {
	// 三角尺寸
	triangleHeight = triangleHeight ? triangleHeight : radius;

	let fill = background;
    let height = outerHeight - triangleHeight;
    let width = outerWidth - triangleHeight;
	let start = `M${radius} 0`;
	let tLine = `L${outerWidth - radius} 0 `;
	let rtRadius = `Q${outerWidth} 0 ${outerWidth} ${radius} `;
	let rLine = `L${outerWidth} ${height - radius} `;
	let rbRadius = `Q${outerWidth} ${height} ${outerWidth - radius} ${height} `;
	let brLine = `L${outerWidth / 2 + triangleHeight / 1.2} ${height} `;
	let bTriangle = `L${outerWidth / 2} ${height + triangleHeight} L${outerWidth / 2 - triangleHeight / 1.2} ${height} `;
	let blLine = `L${radius} ${height} `;
	let lbRadius = `Q0 ${height} 0 ${height - radius} `;
	let lLine = `L0 ${radius} `;
	let ltRadius = `Q0 0 ${radius} 0 Z`;

    let tlLine,tTriangle,trLine,bLine,rtLine,rTriangle,rbLine,lbLine,lTriangle,d,svgwidth,svgheight;

    // svg
	switch (new_dir){
		case 'top'://提示框方向默认在元素上方弹出,箭头在下
            d = start + tLine + rtRadius + rLine + rbRadius + brLine + bTriangle + blLine + lbRadius + lLine + ltRadius;
            break;
        case 'top-left':
            brLine = `L${radius+triangleHeight+2*triangleHeight / 1.2} ${height} `;
            bTriangle = `L${radius+triangleHeight+triangleHeight / 1.2} ${height + triangleHeight} L${radius+triangleHeight} ${height} `;
            d = start + tLine + rtRadius + rLine + rbRadius + brLine + bTriangle + blLine + lbRadius + lLine + ltRadius;
            break;
        case 'top-right':
            brLine = `L${outerWidth-radius-triangleHeight-2*triangleHeight / 1.2} ${height} `;
            bTriangle = `L${outerWidth-radius-triangleHeight-triangleHeight / 1.2} ${height + triangleHeight} L${outerWidth-radius-triangleHeight} ${height} `;
            d = start + tLine + rtRadius + rLine + rbRadius + brLine + bTriangle + blLine + lbRadius + lLine + ltRadius;
            break;
        case 'bottom':
            start = `M${radius} ${triangleHeight}`;
            tlLine = `L${outerWidth / 2 - triangleHeight / 1.2} ${triangleHeight} `;
            tTriangle = `L${outerWidth / 2} 0 L${outerWidth / 2 + triangleHeight / 1.2} ${triangleHeight} `;
            trLine = `L${outerWidth - radius} ${triangleHeight} `;
            rtRadius = `Q${outerWidth} ${triangleHeight} ${outerWidth} ${radius+triangleHeight} `;
            rLine = `L${outerWidth} ${height+triangleHeight - radius} `;
            rbRadius = `Q${outerWidth} ${height+triangleHeight} ${outerWidth - radius} ${height+triangleHeight} `;
            blLine = `L${radius} ${height+triangleHeight} `;
            lbRadius = `Q0 ${height+triangleHeight} 0 ${height+triangleHeight - radius} `;
            lLine = `L0 ${radius+triangleHeight} `;
            ltRadius = `Q0 ${triangleHeight} ${radius} ${triangleHeight} Z`;
            d = start + tlLine + tTriangle + trLine + rtRadius + rLine + rbRadius + blLine + lbRadius + lLine + ltRadius;
            break;
        case 'bottom-left':
            start = `M${radius} ${triangleHeight}`;
            tlLine = `L${radius+triangleHeight} ${triangleHeight} `;
            tTriangle = `L${radius+triangleHeight+triangleHeight / 1.2} 0 L${radius+triangleHeight+2*triangleHeight / 1.2} ${triangleHeight} `;
            trLine = `L${outerWidth - radius} ${triangleHeight} `;
            rtRadius = `Q${outerWidth} ${triangleHeight} ${outerWidth} ${radius+triangleHeight} `;
            rLine = `L${outerWidth} ${height+triangleHeight - radius} `;
            rbRadius = `Q${outerWidth} ${height+triangleHeight} ${outerWidth - radius} ${height+triangleHeight} `;
            blLine = `L${radius} ${height+triangleHeight} `;
            lbRadius = `Q0 ${height+triangleHeight} 0 ${height+triangleHeight - radius} `;
            lLine = `L0 ${radius+triangleHeight} `;
            ltRadius = `Q0 ${triangleHeight} ${radius} ${triangleHeight} Z`;
            d = start + tlLine + tTriangle + trLine + rtRadius + rLine + rbRadius + blLine + lbRadius + lLine + ltRadius;
            break;
        case 'bottom-right':
            start = `M${radius} ${triangleHeight}`;
            tlLine = `L${outerWidth-radius-triangleHeight-2*triangleHeight / 1.2} ${triangleHeight} `;
            tTriangle = `L${outerWidth-radius-triangleHeight-triangleHeight / 1.2} 0 L${outerWidth-radius-triangleHeight} ${triangleHeight} `;
            trLine = `L${outerWidth - radius} ${triangleHeight} `;
            rtRadius = `Q${outerWidth} ${triangleHeight} ${outerWidth} ${radius+triangleHeight} `;
            rLine = `L${outerWidth} ${height+triangleHeight - radius} `;
            rbRadius = `Q${outerWidth} ${height+triangleHeight} ${outerWidth - radius} ${height+triangleHeight} `;
            blLine = `L${radius} ${height+triangleHeight} `;
            lbRadius = `Q0 ${height+triangleHeight} 0 ${height+triangleHeight - radius} `;
            lLine = `L0 ${radius+triangleHeight} `;
            ltRadius = `Q0 ${triangleHeight} ${radius} ${triangleHeight} Z`;
            d = start + tlLine + tTriangle + trLine + rtRadius + rLine + rbRadius + blLine + lbRadius + lLine + ltRadius;
            break;
        case 'left':
            tLine = `L${width - radius} 0 `;
            rtRadius = `Q${width} 0 ${width} ${radius} `;
            rtLine = `L${width} ${outerHeight / 2 - triangleHeight / 1.2} `;
            rTriangle = `L${width + triangleHeight} ${outerHeight / 2} L${width} ${outerHeight / 2 + triangleHeight / 1.2} `;
            rbLine = `L${width} ${outerHeight - radius} `;
            rbRadius = `Q${width} ${outerHeight} ${width - radius} ${outerHeight} `;
            bLine = `L${radius} ${outerHeight} `;
            lbRadius = `Q0 ${outerHeight} 0 ${outerHeight - radius} `;
            d = start + tLine + rtRadius + rtLine + rTriangle + rbLine + rbRadius + bLine + lbRadius  + lLine + ltRadius;
            break;
        case 'left-top':
            tLine = `L${width - radius} 0 `;
            rtRadius = `Q${width} 0 ${width} ${radius} `;
            rtLine = `L${width} ${radius+triangleHeight} `;
            rTriangle = `L${width + triangleHeight} ${radius+triangleHeight+triangleHeight/1.2} L${width} ${radius+triangleHeight+2*triangleHeight/1.2} `;
            rbLine = `L${width} ${outerHeight - radius} `;
            rbRadius = `Q${width} ${outerHeight} ${width - radius} ${outerHeight} `;
            bLine = `L${radius} ${outerHeight} `;
            lbRadius = `Q0 ${outerHeight} 0 ${outerHeight - radius} `;
            d = start + tLine + rtRadius + rtLine + rTriangle + rbLine + rbRadius + bLine + lbRadius  + lLine + ltRadius;
            break;
        case 'left-bottom':
            tLine = `L${width - radius} 0 `;
            rtRadius = `Q${width} 0 ${width} ${radius} `;
            rtLine = `L${width} ${outerHeight-radius-triangleHeight-2*triangleHeight/1.2} `;
            rTriangle = `L${width + triangleHeight} ${outerHeight-radius-triangleHeight-triangleHeight/1.2} L${width} ${outerHeight-radius-triangleHeight} `;
            rbLine = `L${width} ${outerHeight - radius} `;
            rbRadius = `Q${width} ${outerHeight} ${width - radius} ${outerHeight} `;
            bLine = `L${radius} ${outerHeight} `;
            lbRadius = `Q0 ${outerHeight} 0 ${outerHeight - radius} `;
            d = start + tLine + rtRadius + rtLine + rTriangle + rbLine + rbRadius + bLine + lbRadius  + lLine + ltRadius;
            break;
        case 'right':
            start = `M${radius+triangleHeight} 0`;
            tLine = `L${width+triangleHeight - radius} 0 `;
            rtRadius = `Q${width+triangleHeight} 0 ${width+triangleHeight} ${radius} `;
            rLine = `L${width+triangleHeight} ${outerHeight - radius} `;
            rbRadius = `Q${width+triangleHeight} ${outerHeight} ${width+triangleHeight - radius} ${outerHeight} `;
            bLine = `L${radius+triangleHeight} ${outerHeight} `;
            lbRadius = `Q${triangleHeight} ${outerHeight} ${triangleHeight} ${outerHeight - radius} `;
            lbLine = `L${triangleHeight} ${outerHeight / 2 + triangleHeight / 1.2} `;
            lTriangle = `L0 ${outerHeight / 2} L${triangleHeight} ${outerHeight / 2 - triangleHeight / 1.2} `;
            lLine = `L${triangleHeight} ${radius} `;
            ltRadius = `Q${triangleHeight} 0 ${radius+triangleHeight} 0 `;
            d = start + tLine + rtRadius + rLine + rbRadius + bLine + lbRadius + lbLine + lTriangle + lLine + ltRadius;
            break;
        case 'right-top':
            start = `M${radius+triangleHeight} 0`;
            tLine = `L${width+triangleHeight - radius} 0 `;
            rtRadius = `Q${width+triangleHeight} 0 ${width+triangleHeight} ${radius} `;
            rLine = `L${width+triangleHeight} ${outerHeight - radius} `;
            rbRadius = `Q${width+triangleHeight} ${outerHeight} ${width+triangleHeight - radius} ${outerHeight} `;
            bLine = `L${radius+triangleHeight} ${outerHeight} `;
            lbRadius = `Q${triangleHeight} ${outerHeight} ${triangleHeight} ${outerHeight - radius} `;
            lbLine = `L${triangleHeight} ${radius+triangleHeight+2*triangleHeight / 1.2} `;
            lTriangle = `L0 ${radius+triangleHeight+triangleHeight / 1.2} L${triangleHeight} ${radius+triangleHeight} `;
            lLine = `L${triangleHeight} ${radius} `;
            ltRadius = `Q${triangleHeight} 0 ${radius+triangleHeight} 0 `;
            d = start + tLine + rtRadius + rLine + rbRadius + bLine + lbRadius + lbLine + lTriangle + lLine + ltRadius;
            break;
        case 'right-bottom':
            start = `M${radius+triangleHeight} 0`;
            tLine = `L${width+triangleHeight - radius} 0 `;
            rtRadius = `Q${width+triangleHeight} 0 ${width+triangleHeight} ${radius} `;
            rLine = `L${width+triangleHeight} ${outerHeight - radius} `;
            rbRadius = `Q${width+triangleHeight} ${outerHeight} ${width+triangleHeight - radius} ${outerHeight} `;
            bLine = `L${radius+triangleHeight} ${outerHeight} `;
            lbRadius = `Q${triangleHeight} ${outerHeight} ${triangleHeight} ${outerHeight - radius} `;
            lbLine = `L${triangleHeight} ${outerHeight-radius-triangleHeight} `;
            lTriangle = `L0 ${outerHeight-radius-triangleHeight-triangleHeight / 1.2} L${triangleHeight} ${outerHeight-radius-triangleHeight-2*triangleHeight / 1.2} `;
            lLine = `L${triangleHeight} ${radius} `;
            ltRadius = `Q${triangleHeight} 0 ${radius+triangleHeight} 0 `;
            d = start + tLine + rtRadius + rLine + rbRadius + bLine + lbRadius + lbLine + lTriangle + lLine + ltRadius;
            break;
        default:
        d = start + tLine + rtRadius + rLine + rbRadius + brLine + bTriangle + blLine + lbRadius + lLine + ltRadius;
    }

    svgwidth = outerWidth;
    svgheight = outerHeight;
	let svg = `<svg width='${svgwidth}' height='${svgheight}' xmlns='http://www.w3.org/2000/svg'><path d='${d}' stroke-width='${strokeWidth}' fill='${fill}'/></svg>`;

	return svg;
}

export const setBackround = ({$target, radius = 4, triangleHeight = defaultTriangleH, background,new_dir}) => {

    //
    let outerWidth = $target.outerWidth();
    let outerHeight = $target.outerHeight();
    // 三角尺寸
    triangleHeight = triangleHeight ? triangleHeight : radius;
    //
    let svgHtml = createRoundRectSvg({outerWidth, outerHeight, radius, triangleHeight, background,new_dir});
    let bgImage = 'url("data:image/svg+xml,' + encodeURI(svgHtml) + '") no-repeat';
    $target.css("background", bgImage);


    return $target;
}

//
export const textTooltip = (tooltipHtml,$outtarget,tooldirection) => {
	let $target = $(tpl(tooltipTemplate));
    let $content = $target.find(contentClass);
	$content.html(tooltipHtml);
	// append
	$target.appendTo("body");
	// line-height
	//$content.css("lineHeight", $content.height() + "px");

    //判断显示位置，如果空间不足，改变方向
    let _toolHeight = $target.outerHeight();
    let _toolWidth = $target.outerWidth()+20;
    let _offset = $outtarget.offset();
    let _targetouterHeight = $outtarget.outerHeight();
    let _targetouterWidth = $outtarget.outerWidth();
    let mleft,mright,mtop,mbottom,new_dir;

	//判断 方向
    switch (tooldirection){
        case 'top':
            mleft = _offset.left + _targetouterWidth/2 - _toolWidth/2;
            mtop = _offset.top - _toolHeight;
			mright = _offset.left + _toolWidth;
            new_dir = direccheck('top',mtop,mright,0,mleft);
            break;
        case 'top-right':
            mleft = _offset.left + _targetouterWidth - _toolWidth;
            mtop = _offset.top - _toolHeight;
            new_dir = direccheck('top-right',mtop,0,0,mleft);
            break;
        case 'top-left':
            mright = _offset.left + _toolWidth;
            mtop = _offset.top - _toolHeight;
            new_dir = direccheck('top-left',mtop,mright,0,0);
            break;
        case 'bottom':
            mleft = _offset.left + _targetouterWidth/2 - _toolWidth/2;
            mbottom = _offset.top + _targetouterHeight + _toolHeight;
			mright = _offset.left + _toolWidth;
            new_dir = direccheck('bottom',0,mright,mbottom,mleft);
            break;
        case 'bottom-right':
            mleft = _offset.left + _targetouterWidth - _toolWidth;
            mbottom = _offset.top + _targetouterHeight + _toolHeight;
            new_dir = direccheck('bottom-right',0,0,mbottom,mleft);
            break;
        case 'bottom-left':
            mright = _offset.left + _toolWidth;
            mbottom = _offset.top + _targetouterHeight + _toolHeight;
            new_dir = direccheck('bottom-left',0,mright,mbottom,0);
            break;
        case 'left':
            mleft = _offset.left - _toolWidth;
            mtop = _offset.top + _targetouterHeight/2 - _toolHeight/2;
            mbottom = _offset.top + _targetouterHeight/2 + _toolHeight/2;
            new_dir = direccheck('left',mtop,0,mbottom,mleft);
            break;
        case 'left-bottom':
            mleft = _offset.left - _toolWidth;
            mtop = _offset.top + _targetouterHeight - _toolHeight;
            new_dir = direccheck('left-bottom',mtop,0,0,mleft);
            break;
        case 'left-top':
            mleft = _offset.left - _toolWidth;
            mbottom = _offset.top + _toolHeight;
            new_dir = direccheck('left-top',0,0,mbottom,mleft);
            break;
        case 'right':
            mright = _offset.left + _targetouterWidth + _toolWidth;
            mtop = _offset.top + _targetouterHeight/2 - _toolHeight/2;
            mbottom = _offset.top + _targetouterHeight/2 + _toolHeight/2;
            new_dir = direccheck('right',mtop,mright,mbottom,0);
            break;
        case 'right-bottom':
            mright = _offset.left + _targetouterWidth + _toolWidth;
            mtop = _offset.top + _targetouterHeight - _toolHeight;
            new_dir = direccheck('right-bottom',mtop,mright,0,0);
            break;
        case 'right-top':
            mright = _offset.left + _targetouterWidth + _toolWidth;
            mbottom = _offset.top + _toolHeight;
            new_dir = direccheck('right-top',0,mright,mbottom,0);
            break;
        default:
			mleft = _offset.left + _targetouterWidth/2 - _toolWidth/2;
			mtop = _offset.top - _toolHeight;
			mright = _offset.left + _toolWidth;
			new_dir = direccheck('top',mtop,mright,0,mleft);
            break;
    }



    // 外层容器根据箭头方向 padding不同
    let targetpadtb = (8+defaultTriangleH)+'px';
    let targetpadlr = (10+defaultTriangleH)+'px';
    $target.css({
        "padding-top":'8px',
        "padding-bottom":'8px',
        "padding-left":'10px',
        "padding-right":'10px',
    });
    switch (new_dir){
        case 'top':
        case 'top-left':
        case 'top-right':
            $target.css("padding-bottom", targetpadtb);
			$target.addClass('originBottom');
            break;
        case 'bottom':
        case 'bottom-left':
        case 'bottom-right':
            $target.css("padding-top", targetpadtb);
			$target.addClass('originTop');
            break;
        case 'left':
        case 'left-top':
        case 'left-bottom':
            $target.css("padding-right", targetpadlr);
			$target.addClass('originRight');
            break;
        case 'right':
        case 'right-top':
        case 'right-bottom':
            $target.css("padding-left", targetpadlr);
			$target.addClass('originLeft');
            break;
        default:
            $target.css({
                "padding-top":'8px',
                "padding-bottom":'8px',
                "padding-left":'10px',
                "padding-right":'10px',
            });
			$target.addClass('originBottom');
    }



    // 外层容器高度
    let targetHeight;
    targetHeight = $content.outerHeight();
    $target.height(targetHeight);
    //$target.height($content.outerHeight() + defaultTriangleH);



    // add background
	setBackround({$target,new_dir});

    // 设置坐标
    setPosition($outtarget, $target , new_dir);

    // return
	return $target;
}




// 方向判断，如果超出，改变方向，返回值为新方向(正确的方向)
export const direccheck = (olddir,mtop, mright, mbottom, mleft) => {
    let bodyW = document.documentElement.clientWidth;
    let bodyH = document.documentElement.clientHeight;
    let new_dircheck;
    new_dircheck = olddir;
    //左侧不够了、右侧不够了
	if(mleft<0 || mright>bodyW){
        if(new_dircheck.indexOf("right") >= 0 ){
            new_dircheck = new_dircheck.replace('right','left');
        }else if(new_dircheck.indexOf("left") >= 0 ){
            new_dircheck = new_dircheck.replace('left','right');
        }else{
            if(mleft<0){
                new_dircheck = new_dircheck+'-left';
            }else if(mright>bodyW){
                new_dircheck = new_dircheck+'-right';
            }
        }
    }

    //上面不够了、下面不够了
    if(mtop<0 || mbottom>bodyH){
        if(new_dircheck.indexOf("bottom") >= 0 ){
            new_dircheck = new_dircheck.replace('bottom','top');
        }else if(new_dircheck.indexOf("top") >= 0 ){
            new_dircheck = new_dircheck.replace('top','bottom');
        }else{
            if(mtop<0){
                new_dircheck = new_dircheck+'-top';
            }else if(mbottom>bodyH){
                new_dircheck = new_dircheck+'-bottom';
            }
        }
    }

    return new_dircheck;
}





// 设置坐标
export const setPosition = ($target, $tooltip, direction) => {
    let _outerHeight = $tooltip.outerHeight();
    let _outerWidth = $tooltip.outerWidth();
    let _offset = $target.offset();
    let _targetouterHeight = $target.outerHeight();
    let _targetouterWidth = $target.outerWidth();
    let left,top;

    switch (direction){
        case 'top'://提示方向默认向上
            left = _offset.left + $target.outerWidth() / 2 - $tooltip.outerWidth() / 2;
            top = _offset.top - _outerHeight;
            break;
        case 'top-left':
            left = _offset.left;
            top = _offset.top - _outerHeight;
            break;
        case 'top-right':
            left = _offset.left+_targetouterWidth-$tooltip.outerWidth();
            top = _offset.top - _outerHeight;
            break;
        case 'bottom':
            left = _offset.left + $target.outerWidth() / 2 - $tooltip.outerWidth() / 2;
            top = _offset.top + _targetouterHeight;
            break;
        case 'bottom-left':
            left = _offset.left;
            top = _offset.top + _targetouterHeight;
            break;
        case 'bottom-right':
            left = _offset.left+_targetouterWidth-$tooltip.outerWidth();
            top = _offset.top + _targetouterHeight;
            break;
        case 'left':
            left = _offset.left - $tooltip.outerWidth() ;
            top = _offset.top + $target.outerHeight() / 2 - $tooltip.outerHeight() / 2;
            break;
        case 'left-top':
            left = _offset.left - $tooltip.outerWidth() ;
            top = _offset.top;
            break;
        case 'left-bottom':
            left = _offset.left - $tooltip.outerWidth() ;
            top = _offset.top + _targetouterHeight - $tooltip.outerHeight();
            break;
        case 'right':
            left = _offset.left + _targetouterWidth ;
            top = _offset.top + $target.outerHeight() / 2 - $tooltip.outerHeight() / 2;
            break;
        case 'right-top':
            left = _offset.left + _targetouterWidth ;
            top = _offset.top;
            break;
        case 'right-bottom':
            left = _offset.left + _targetouterWidth ;
            top = _offset.top + _targetouterHeight - $tooltip.outerHeight();
            break;
        default:
            left = _offset.left + $target.outerWidth() / 2 - $tooltip.outerWidth() / 2;
            top = _offset.top - _outerHeight;
    }

    $tooltip.css({left, top});
}




//
export const jqueryObjTooltip = (ops) => {
	let radius, triangleH, fill, $target;
	let {$wrap, html} = ops;
	if ($wrap && !isJq($wrap)) {
		throw new Error(`v.tooltip参数错误,$wrap必须是jquery对象`);
	}
	isNumber(ops.radius) && (radius = ops.radius);
	isNumber(ops.triangleHeight) && (triangleH = ops.triangleHeight);
	$target = $wrap;
	// append
	$target.appendTo("body");
}
//ops ={html, $wrap, radius, opacity, triangleHeight, background, padding}
//ops ="文本信息"
const tooltipSvg = (ops) => {
    let $target;
    //
    if (isString(ops)) {
        $target = textTooltip(ops);
        // setBackround
        setBackround({$target});
    } else if (isJson(ops)) {
        if (isJq(ops.$wrap)) {
            let {$wrap, radius, triangleHeight, background} = ops;
            $target = $wrap;
            if (background) {
                if (rgbaReg.test(background)) {
                    background = background.replace(/\s*/, "");
                } else {
                    console.error("v.tooltip参数错误, background值必须是rgba(r,g,b,a)形式");
                }
            }
            // append
            $wrap.appendTo("body");
            //
            setBackround({$target, radius, triangleHeight, background});
        } else {
            throw new Error("v.tooltip()参数错误,$wrap必须是jquery对象");
        }
    }
    // return
    return $target;
}


