'use strict';

import {isUndefined} from "./common/util";
import getTarget from "./common/getTarget";

function center($target, el) {
	// console.log("center.js");
	let $holder;
	if (isUndefined(el)) {
		$holder = $(window);
	} else {
		$holder = getTarget(el);
	}
	$holder.on("resize", function (e) {
		let w = $holder.width();
		let h = $holder.height();
		let left = Math.floor((w - $target.width()) / 2);
		let top = Math.floor((h - $target.height()) / 2);
		$target.css({
			left: left,
			top: top
		})
	}).trigger("resize");
}


export default center;