'use strict';


//
import {isString, isFunction, isNull, isUndefined} from "../common/util";


//
const expandTrClass = "v-table-expanded-tr",
	checkboxClass = "v-table-checkbox",
	rowDataReg = /^rowData\./,
	checkboxReg = /^table\.checkbox/,
	checkboxHtml = `<span class="v-check">
					    <input type="checkbox" class="v-check-input v-table-checkbox" value="">
					    <div class="v-check-inner"></div><div class="v-check-text"></div>
					</span>`;


const createTd = ({_root, trIndex, $leftTr, $centerTr, $rightTr, rowData, bodyColumn}) => {
	// let {trIndex, render, fixed, align, valign, tdClass, rowData, data} = bodyColumn;
	//
	let {render, title, align, valign, tdClass} = bodyColumn,
		{
			initData: {onCheckedChange}, leftColumns, bodyColumns, rightColumns
		} = _root._data,
		bodyColumnsLength = bodyColumns.length;
	//
	let alignAttr = align ? ` align=${align}` : "",
		tdClassAttr = tdClass ? ` class=${tdClass}` : "",
		valignAttr = valign ? ` valign=${valign}` : "";
	//
	if (checkboxReg.test(render)) {
		alignAttr = ` align="center"`;
		valignAttr = ` valign="middle"`;
	}
	//
	let $td = $(`<td${tdClassAttr}${alignAttr}${valignAttr}></td>`);
	// 有行数据
	if (isString(render)) { // string render
		//
		if (rowDataReg.test(render)) { // rowData.xxx
			let rowDataValue = rowData,
				arr = render.replace(rowDataReg, "").split(".");
			for (let i = 0; i < arr.length; i++) {
				rowDataValue = rowDataValue[arr[i]];
				if (isNull(rowDataValue) || isUndefined(rowDataValue)) {
					rowDataValue = "";
				}
			}
			$td.html(rowDataValue);
		} else if (checkboxReg.test(render)) { // table.checkbox
			let $checkboxWrap = $(`${checkboxHtml}`).appendTo($td),
				$checkbox = $checkboxWrap.find(`.${checkboxClass}`);
			// click
			$checkboxWrap.on("click", () => {
				let isChecked = $checkbox.prop("checked"),
					$tr = _root.$allTr(trIndex),
					// set main checkbox
					isCheckAll = _root.setMainCheckbox();
				//
				if (isFunction(onCheckedChange)) {
					let checkedChangeData = {
						isCheckAll, isChecked, rowData, $checkbox, $tr, $td, $centerTr, $leftTr, $rightTr, trIndex
					};
					// callback
					onCheckedChange(checkedChangeData, _root);
				}
			})
		} else {
			$td.html(render);
		}
	} else if (isFunction(render)) { //function render
		let $expandTr, $expandTd, toggle;
		let {pageNumber, pageSize} = _root;
		// 当前行数据行索引
		let index = trIndex + (pageNumber - 1) * pageSize + 1;
		let data = {
			rowData, $tr: _root.$allTr(trIndex), $td, trIndex, index, $centerTr, $leftTr, $rightTr,
			get expandRow() {
				if (leftColumns === false && rightColumns === false) {
					let $nextTr = $centerTr.next();
					// call expandRow() first
					if (!$nextTr.is(`.${expandTrClass}`)) {
						$expandTr = $(`<tr class="${expandTrClass}" style="display:none;"></tr>`);
						$expandTd = $(`<td colspan="${bodyColumnsLength}"></td>`);
						$expandTd.appendTo($expandTr);
						$expandTr.insertAfter($centerTr);
						// toggle
						toggle = () => {
							$expandTr.is("tr:visible") ? $expandTr.hide() : $expandTr.show();
							// 切换扩展行可能由于滚动条导致表头无法对齐，调用layout可以解决这个问题
							_root.layout();
						}
					}
					return {
						$tr: $expandTr, $td: $expandTd, toggle
					};
				} else {
					let tt = $(title).text();
					throw new Error(`v.table()错误;有左右锁定列时,禁止使用expandRow功能; '${tt}'列 render方法中调用了expandRow()功能.`);
				}
			}
		};
		render(data, _root);
	}
	//
	return $td;
}

export default createTd;

