'use strict';


import Base from "../common/Base";
import {isArray, isFunction, isJson, isString} from "../common/util";

const hiddenClass = "v-table-hidden-t",
	checkboxReg = /^table\.checkbox/,
	mainCheckboxClass = "v-table-main-checkbox",
	sortThClass = "v-table-th-sort",
	ascClass = "v-table-th-asc",
	descClass = "v-table-th-desc",
	defaultClass = "v-table-th-default",
	sortHtml = `<span class="v-table-span-sort"><i class="vicon vicon-arrsmallup"></i><i class="vicon vicon-arrsmalldown"></i></span>`,
	checkboxHtml = `<span class="v-check">
					    <input type="checkbox" class="v-check-input v-table-main-checkbox" value="">
					    <div class="v-check-inner"></div><div class="v-check-text"></div>
					</span>`;


// sortfield,sortorder

class Thead extends Base {
	constructor(_root) {
		super();
		this._data = {_root};
	}

	init() {
		let {_root} = this._data,
			{bodyColumns, _$lHThead, _$cHThead, _$rHThead} = _root._data,
			tr = "<tr>",
			$leftTr = $(tr), $bodyTr = $(tr), $rightTr = $(tr);
		//
		if (isArray(bodyColumns) && bodyColumns.length > 0) {
			for (let {title, fixed, sort, sortField} of bodyColumns) {
				let $th = $("<th>"), $lTh, $cTh, $rTh, sortData;
				//
				if (isString(title) && !checkboxReg.test(title)) { // 字符串
					$th.html(title);
				} else if (checkboxReg.test(title)) { // checkbox
					let $checkboxWrap = $(`${checkboxHtml}`).appendTo($th),
						$checkbox = $checkboxWrap.find(`.${mainCheckboxClass}`);
					// click
					$checkboxWrap.on("click", () => {
						let isChecked = $checkbox.prop("checked");
						// set checkbox
						_root.checkRows(isChecked);
					})
				} else if (isFunction(title)) { // 方法
					let data = {title, fixed, $th};
					title(data, _root);
				}
				//
				switch (fixed) {
					case "left":
						$lTh = $th;
						$cTh = $th.clone().addClass(hiddenClass);
						$rTh = $th.clone().addClass(hiddenClass);
						sortData = {_root, $th: $lTh, sort, title, sortField};
						isFunction(sort) && this.sortHandler(sortData);
						break;
					case "right":
						$lTh = $th.clone().addClass(hiddenClass);
						$cTh = $th.clone().addClass(hiddenClass);
						$rTh = $th;
						sortData = {_root, $th: $rTh, sort, title, sortField};
						isFunction(sort) && this.sortHandler(sortData);
						break;
					default:
						$lTh = $th.clone().addClass(hiddenClass);
						$cTh = $th;
						$rTh = $th.clone().addClass(hiddenClass);
						sortData = {_root, $th: $cTh, sort, title, sortField};
						isFunction(sort) && this.sortHandler(sortData);
						break;
				}
				$lTh.appendTo($leftTr);
				$cTh.appendTo($bodyTr);
				$rTh.appendTo($rightTr);
			}
			// set html
			$leftTr.appendTo(_$lHThead);
			$bodyTr.appendTo(_$cHThead);
			$rightTr.appendTo(_$rHThead);
		}
	}

	// 表格排序方法；ASC 从小到大；DESC 从大到小(降序)
	sortHandler({_root, $th, sort, title, sortField}) {
		$th.addClass(sortThClass);
		$(sortHtml).appendTo($th);
		$th.on("click", () => {
			let sortOrder,
				{footer} = _root._data,
				titleText = $(`<div>${title}</div>`).text();
			// 排序样式处理
			if ($th.hasClass(ascClass)) { // 当前是升序
				$th.removeClass(ascClass).addClass(descClass);
				sortOrder = "desc";
				footer.orderInfo(`按 '${titleText}' 降序排序`);
			} else if ($th.hasClass(descClass)) {// 当前是降序
				$th.removeClass(descClass).addClass(defaultClass);
				sortOrder = "default";
				footer.orderInfo(`默认排序`);
			} else { //默认
				$th.addClass(ascClass);
				sortOrder = "asc";
				footer.orderInfo(`按 '${titleText}' 升序排序`);
			}
			// sort
			let data = {sortorder: sortOrder, sortOrder, sortfield: sortField, sortField: sortField, $th};
			sort(data, _root);
		})
	}

	// 排序相关处理
	sortBy({sort, field}) {

	}

	noDataHandler() {
		let {_root, _root: {$mainCheckbox}} = this._data;
		// 无数据 disabled
		$mainCheckbox.attr("disabled", _root.dataSource.data.length < 1);
	}
}

export default Thead;
