'use strict';


const defaultDelay = 250,// 监听间隔毫秒
	maxDelaySeconds = 10;// dom挂载等待最大秒数

import {isDetached} from "./util";

// 等待dom挂载
const isMounted = async ($this, delay = defaultDelay) => {
	// console.log("mounted()");
	let currDelaySecond = 0,
		//
		intervalId;
	//
	return await new Promise((resolve, reject) => {
		//
		let mountedListener = () => {
			currDelaySecond += delay;
			// console.log("mountedListener()");
			// 开始计时
			if (currDelaySecond < maxDelaySeconds * 1000) { // dom挂载等待未超时
				if (!isDetached($this)) {
					// console.log("dom 挂载完毕");
					clearInterval(intervalId);
					resolve();
				}
			} else { // dom挂载等待超时
				// console.log("dom挂载等待超时");
				clearInterval(intervalId);
				reject();
			}
		}
		// setInterval
		intervalId = setInterval(() => {
			mountedListener();
		}, delay)
	})
}

export default isMounted;
