'use strict';


import Base from "./common/Base";

import {isUndefined, isJq, isFunction} from "./common/util";

function requireMent(root) {
	let {_$target} = root._data;
	if (!isJq(_$target) || _$target.length !== 1) {
		throw new Error("v.drag方法参数错误,被拖动元素不存在或者不唯一;")
	}
}

// 添加Dom参数
function appendDomValue(root, data) {
	root._data._$target = $(data.target);
}

// 添加默认参数
function appendOtherValue(root, data) {
}

function jqueryDragPlugin() {
	if (isUndefined($.fn.v_drag)) {
		$.fn.v_drag = function (options) {
			let defaults = {};
			options = $.extend(defaults, options);
			//
			let {dragStart, dragEnd, drag, handleClass} = options;
			let $this = $(this);
			let $handle = isUndefined(handleClass) ? $this : $this.find(handleClass);

			$handle.each(function (index, element) {
				let xx, yy, position;
				$handle.on("mousedown.v_drag", function (e) {
					e.preventDefault();
					position = $this.position();
					xx = e.pageX - position.left;
					yy = e.pageY - position.top;
					$(document).on("mousemove.v_drag", mouseMoveHandler);
					$(document).on("mouseup.v_drag", documentMouseUpHandler);
					if (isFunction(dragStart)) {
						dragStart(position.left, position.top);
					}
				});

				function documentMouseUpHandler(e) {
					setCoordinate(e);
					$(document).off("mouseup.v_drag").off("mousemove.v_drag");
					if (isFunction(dragEnd)) {
						position = $this.position();
						dragEnd(position.left, position.top);
					}
				}

				function mouseMoveHandler(e) {
					setCoordinate(e);
				}

				function setCoordinate(e) {
					let tempX = e.pageX - xx;
					let tempY = e.pageY - yy;
					tempX = tempX + $this.outerWidth(true) > $(window).width() ? $(window).width() - $this.outerWidth(true) : tempX;
					tempY = tempY > $(window).height() - $this.height() ? $(window).height() - $this.height() : tempY;
					let targetX = tempX >= 0 ? tempX : 0;
					let targetY = tempY >= 0 ? tempY : 0;
					$this.css({
						"left": targetX,
						"top": targetY
					});
					if (isFunction(drag)) {
						drag(targetX, targetY);
					}
				}
			});

			return ($this);
		}
	}
}

// direction
// handle
// dragStart,dragEnd,dragMove
class Vdrag extends Base {
	constructor(data) {
		super();
		this._data = data;
		appendDomValue(this, data);
		appendOtherValue(this, data);
		requireMent(this);
		jqueryDragPlugin(this);
		let {_$target, handleClass, dragStart, dragEnd, drag} = this._data;
		_$target.v_drag({
			handleClass: handleClass,
			dragStart, dragEnd, drag
		})
	}
}


export default Vdrag;
